<template>
  <offlineAlert @detected-condition="amIOnline">
    <div slot="online">

      <v-snackbar
      v-model="snackbar"
      >
        <p v-if="statusOnline"><v-icon>mdi-wifi-strength-outline</v-icon> Aplicación Online</p>
        <p v-if="!statusOnline"><v-icon>mdi-wifi-strength-off-outline</v-icon> Aplicación Offline</p>
        <template v-slot:action="{ attrs }">
          <v-btn
            :color="colorAlert"
            text
            v-bind="attrs"
            @click="snackbar = false"
          >
            Cerrar
          </v-btn>
        </template>
      </v-snackbar>

    </div>

    <div slot="offline">
    </div>

  </offlineAlert>
</template>

<script>
import offlineAlert from 'v-offline'

export default {
  components: { offlineAlert },
  data: () => ({
    statusOnline: null,
    snackbar: true,
    colorAlert: 'indigo'
  }),
  methods: {
    amIOnline (e) {
      this.statusOnline = e
      this.colorAlert = (e) ? 'indigo' : 'pink'
    }
  }
}

</script>
