<template>
 <div id="chart">
      <v-container
        fluid
        grid-list-lg
      >
        <v-layout row wrap>

          <!-- Costos  directos totales -->
          <v-flex xs12 lg6>
            <v-card color="white" class="dark--text">
              <v-layout>
                <v-flex xs12  ref="barchartDirectos">
                </v-flex>
              </v-layout>
              <v-divider light></v-divider>
              <v-card-actions class="pa-3">
                Costos Totales:
                <v-spacer></v-spacer>
                $ {{ totalActividadCosto }}
              </v-card-actions>
            </v-card>
          </v-flex>

          <!-- Costos  directos por actividad -->
          <v-flex xs12 lg6>
            <v-card color="white" class="dark--text">
              <v-layout>
                <v-flex xs12  ref="barchartInDirectos">
                </v-flex>
              </v-layout>
              <v-divider light></v-divider>
              <v-card-actions class="pa-3">
                Costos Totales:
                <v-spacer></v-spacer>
                $ {{ totalRubroCosto }}
              </v-card-actions>
            </v-card>
          </v-flex>

          <!-- Costos  por tipo maquinaria -->
          <v-flex xs12 lg6>
            <v-card color="white" class="dark--text">
              <v-layout>
                <v-flex xs12  ref="piechartMaquinaria">
                </v-flex>
              </v-layout>
              <v-divider light></v-divider>
              <v-card-actions class="pa-3">
                Costos Totales:
                <v-spacer></v-spacer>
                $ {{ totalMaquinaria }}
              </v-card-actions>
            </v-card>
          </v-flex>

          <!-- porcentaje  por  perdida forestal -->
          <v-flex xs12 lg6>
            <v-card color="white" class="dark--text">
              <v-layout>
                <v-flex xs12  ref="barchartPerdidaForestal">
                </v-flex>
              </v-layout>
              <v-divider light></v-divider>
              <v-card-actions class="pa-3">
                Porcentaje Total:
                <v-spacer></v-spacer>
                % {{ porcePerdidaForestal }}
              </v-card-actions>
            </v-card>
          </v-flex>

          <!-- Costos  por  perdida forestal -->
          <v-flex xs12 lg6>
            <v-card color="white" class="dark--text">
              <v-layout>
                <v-flex xs12  ref="barchartValPerdidaForestal">
                </v-flex>
              </v-layout>
              <v-divider light></v-divider>
              <v-card-actions class="pa-3">
                Costos Totales:
                <v-spacer></v-spacer>
                $ {{ totalPerdidaForestal }}
              </v-card-actions>
            </v-card>
          </v-flex>

          <!-- podentaje  por  perdida estimada -->
          <v-flex xs12 lg6>
            <v-card color="white" class="dark--text">
              <v-layout>
                <v-flex xs12  ref="barchartPorcePerdidaEsti">
                </v-flex>
              </v-layout>
              <v-divider light></v-divider>
              <v-card-actions class="pa-3">
                Porcentaje Total:
                <v-spacer></v-spacer>
                {{ porcePerdidaEsti }}
              </v-card-actions>
            </v-card>
          </v-flex>

        </v-layout>

      </v-container>

 </div>
</template>

<script>

import { informesEvento } from '../../services/api'
import ApexCharts from 'apexcharts'
import jwtDecode from 'jwt-decode'

export default {
  name: 'informesPorEvento',
  data () {
    return {
      dataEvento: [],
      totalActividadCosto: 0,
      totalRubroCosto: 0,
      totalMaquinaria: 0,
      porcePerdidaForestal: 0,
      totalPerdidaForestal: 0,
      porcePerdidaEsti: 0,

      series: [{
        data: []
      }],
      chartOptions: {
        chart: {
          type: 'bar',
          height: 350
        },
        plotOptions: {
          bar: {
            borderRadius: 4,
            horizontal: true
          }
        },
        dataLabels: {
          enabled: true
        },
        xaxis: {
          categories: []
        }
      }

    }
  },
  async mounted () {
    await this.setDataInformes()
  },
  created () {
    this.dataEvento = this.$route.params.evento
  },
  methods: {
    async setDataInformes () {
      const idEvento = this.dataEvento.cod_evento

      try {
        const res = await informesEvento.setDataInformesEvento({ idEvento })

        if (res.status === 200) {
          const encoded = res.data.token
          const decoded = jwtDecode(encoded)
          const data = decoded.dataInformes

          console.log(data)

          this.costosDirectosPorActividad(data.costosDirectos)
          this.costosInDirectosPorRubro(data.costosInDirectos)
          this.costosInfraestructura(data.tipoInfraestructura)
          this.perdidaEconomicaForestal(data.perdidaEconomicaForestal)
          this.valPerdidaEconomica(data.valPerdidaEconomica)
          this.perdidaEstimadaProduccion(data.perdidaEstimadaProdu)
        }
      } catch (error) {
        console.log(error)
      }
    },
    costosDirectosPorActividad (costosDirectos) {
      var data = []
      var categories = []
      var totalCosto = 0

      for (const documento in costosDirectos) {
        data.push(parseFloat(costosDirectos[documento].gasto_incurrido))
        categories.push(costosDirectos[documento].actividad)
        totalCosto += parseFloat(costosDirectos[documento].gasto_incurrido)
      }

      this.totalActividadCosto = (new Intl.NumberFormat('es-ES').format(totalCosto))

      const chart = new ApexCharts(this.$refs.barchartDirectos, {
        title: {
          text: 'Costos directos por actividad',
          align: 'left',
          margin: 10,
          offsetX: 0,
          offsetY: 0,
          floating: false,
          style: {
            fontSize: '14px',
            fontWeight: 'bold',
            fontFamily: undefined,
            color: '#263238'
          }
        },
        series: [{
          data: data
        }],
        chart: {
          type: 'bar',
          height: 300
        },
        grid: {
          xaxis: {
            lines: {
              show: true
            }
          }
        },
        plotOptions: {
          bar: {
            borderRadius: 4,
            horizontal: true
          }
        },
        dataLabels: {
          enabled: true
        },
        xaxis: {
          categories: categories
        }
      })

      chart.render()
    },
    costosInDirectosPorRubro (costosInDirectos) {
      var data = []
      var categories = []
      var totalCosto = 0

      for (const documento in costosInDirectos) {
        data.push(parseFloat(costosInDirectos[documento].gasto_incurrido))
        categories.push(costosInDirectos[documento].rubros)
        totalCosto += parseFloat(costosInDirectos[documento].gasto_incurrido)
      }

      this.totalRubroCosto = (new Intl.NumberFormat('es-ES').format(totalCosto))

      const chart = new ApexCharts(this.$refs.barchartInDirectos, {
        title: {
          text: 'Costos indirectos por rubro',
          align: 'left',
          margin: 10,
          offsetX: 0,
          offsetY: 0,
          floating: false,
          style: {
            fontSize: '14px',
            fontWeight: 'bold',
            fontFamily: undefined,
            color: '#263238'
          }
        },
        series: [{
          data: data
        }],
        chart: {
          type: 'bar',
          height: 300
        },
        grid: {
          xaxis: {
            lines: {
              show: true
            }
          }
        },
        plotOptions: {
          bar: {
            borderRadius: 4,
            horizontal: true
          }
        },
        dataLabels: {
          enabled: true
        },
        xaxis: {
          categories: categories
        }
      })

      chart.render()
    },
    costosInfraestructura (costoInfraestructura) {
      var data = []
      var categories = ['Vlr.Semilla', 'Vlr.Fertilizante', 'Vlr.Plaguicida', 'Vlr.Maquinaria']
      var totalCosto = 0

      for (const documento in costoInfraestructura) {
        const semilla = parseFloat(costoInfraestructura[documento].vlr_semilla)
        const fertilizante = parseFloat(costoInfraestructura[documento].vlr_fertilizante)
        const plaguicida = parseFloat(costoInfraestructura[documento].vlr_plaguicida)
        const maquinaria = parseFloat(costoInfraestructura[documento].vlr_maquinaria)
        data.push(semilla)
        data.push(fertilizante)
        data.push(plaguicida)
        data.push(maquinaria)
        totalCosto += semilla + fertilizante + plaguicida + maquinaria
      }

      this.totalMaquinaria = (new Intl.NumberFormat('es-ES').format(totalCosto))

      const chart = new ApexCharts(this.$refs.piechartMaquinaria, {
        title: {
          text: 'Costos por infraestructura',
          align: 'left',
          margin: 10,
          offsetX: 0,
          offsetY: 0,
          floating: false,
          style: {
            fontSize: '14px',
            fontWeight: 'bold',
            fontFamily: undefined,
            color: '#263238'
          }
        },
        series: data,
        chart: {
          type: 'pie',
          height: 300,
          toolbar: {
            show: true
          }
        },
        labels: categories
      })

      chart.render()
    },
    perdidaEconomicaForestal (porcPerdida) {
      var data = []
      var categories = []
      var totalCosto = 0

      for (const documento in porcPerdida) {
        data.push(parseFloat(porcPerdida[documento].valor))
        categories.push(documento * 1 + 1)
        totalCosto += parseFloat(porcPerdida[documento].valor)
      }

      this.porcePerdidaForestal = (new Intl.NumberFormat('es-ES').format(totalCosto))

      const chart = new ApexCharts(this.$refs.barchartPerdidaForestal, {
        title: {
          text: '% Perdida plantación forestal',
          align: 'left',
          margin: 10,
          offsetX: 0,
          offsetY: 0,
          floating: false,
          style: {
            fontSize: '14px',
            fontWeight: 'bold',
            fontFamily: undefined,
            color: '#263238'
          }
        },
        series: [{
          data: data
        }],
        chart: {
          type: 'bar',
          height: 300
        },
        grid: {
          xaxis: {
            lines: {
              show: true
            }
          }
        },
        plotOptions: {
          bar: {
            borderRadius: 4,
            horizontal: true
          }
        },
        dataLabels: {
          enabled: true
        },
        xaxis: {
          categories: categories
        }
      })

      chart.render()
    },
    valPerdidaEconomica (costoPerdida) {
      var data = []
      var categories = []
      var totalCosto = 0

      for (const documento in costoPerdida) {
        data.push(parseFloat(costoPerdida[documento].valor))
        categories.push(documento * 1 + 1)
        totalCosto += parseFloat(costoPerdida[documento].valor)
      }

      this.totalPerdidaForestal = (new Intl.NumberFormat('es-ES').format(totalCosto))

      const chart = new ApexCharts(this.$refs.barchartValPerdidaForestal, {
        title: {
          text: 'Valor perdida plantación forestal',
          align: 'left',
          margin: 10,
          offsetX: 0,
          offsetY: 0,
          floating: false,
          style: {
            fontSize: '14px',
            fontWeight: 'bold',
            fontFamily: undefined,
            color: '#263238'
          }
        },
        series: [{
          data: data
        }],
        chart: {
          type: 'bar',
          height: 300
        },
        grid: {
          xaxis: {
            lines: {
              show: true
            }
          }
        },
        plotOptions: {
          bar: {
            borderRadius: 4,
            horizontal: true
          }
        },
        dataLabels: {
          enabled: true
        },
        xaxis: {
          categories: categories
        }
      })

      chart.render()
    },
    perdidaEstimadaProduccion (porcePerdidaEstimada) {
      var data = []
      var categories = []
      var totalCosto = 0

      for (const documento in porcePerdidaEstimada) {
        if (parseFloat(porcePerdidaEstimada[documento].valor) < 0) {
          continue
        }
        data.push(parseFloat(porcePerdidaEstimada[documento].valor))
        categories.push(documento * 1 + 1)
        totalCosto += parseFloat(porcePerdidaEstimada[documento].valor)
      }

      this.porcePerdidaEsti = totalCosto ? '%' + (new Intl.NumberFormat('es-ES').format(totalCosto)) : 'No hay perdida'

      const chart = new ApexCharts(this.$refs.barchartPorcePerdidaEsti, {
        title: {
          text: '% perdida estimada',
          align: 'left',
          margin: 10,
          offsetX: 0,
          offsetY: 0,
          floating: false,
          style: {
            fontSize: '14px',
            fontWeight: 'bold',
            fontFamily: undefined,
            color: '#263238'
          }
        },
        series: [{
          data: data
        }],
        chart: {
          type: 'bar',
          height: 300
        },
        grid: {
          xaxis: {
            lines: {
              show: true
            }
          }
        },
        plotOptions: {
          bar: {
            borderRadius: 4,
            horizontal: true
          }
        },
        dataLabels: {
          enabled: true
        },
        xaxis: {
          categories: categories
        }
      })

      chart.render()
    }
  }
}
</script>
