import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import inicio from '../views/inicio.vue'
import newEvento from '../views/newEvent.vue'
import updateUser from '../views/updateUser.vue'
import seguimiento from '../views/seguimiento.vue'
import informesEvento from '../views/informesEvento.vue'

Vue.use(VueRouter)

var self = Vue.prototype

const routes = [
  {
    path: '/inicio',
    name: 'inicio',
    component: inicio
  },
  {
    path: '/home',
    name: 'listadoEventos',
    component: Home
  },
  {
    path: '/newEvento',
    name: 'nuevoEvento',
    component: newEvento
  },
  {
    path: '/newEvento/:evento',
    name: 'actualizarEevento',
    component: newEvento
  },
  {
    path: '/updateUser',
    name: 'editarUsuario',
    component: updateUser
  },
  {
    path: '/seguimiento/:evento',
    name: 'seguimiento',
    props: true,
    component: seguimiento
  },
  {
    path: '/informesEvento/:evento',
    name: 'informesEvento',
    props: true,
    component: informesEvento
  },
  { path: '*', redirect: '/home' },
  {
    path: '/about',
    name: 'About',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ '../views/About.vue')
  },
  {
    path: '/logout',
    name: 'logout',
    component: {
      beforeRouteEnter (to, from, next) {
        const loggedIn = self.$session.exists()
        if (loggedIn) {
          self.$session.destroy()
          next('/inicio')
        }
      }
    }

  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})
router.beforeEach((to, from, next) => {
  // console.log(to)
  const loggedIn = self.$session.exists()
  if (loggedIn) {
    const idRol = self.$session.get('id_rol')
    if (to.name === 'admin') {
      if ([1, 2].includes(idRol)) {
        next() // pasa a admin ya que cumple la ruta y su rol esta entre admin y moderador
      } else {
        self.$session.destroy()
        next('/inicio') // su rol no es admin o moderador. Se cierra sesion y se envia a login
      }
    } else {
      next()
    }
  } else {
    console.log('not logged')
    if (to.name === 'map') {
      next()
    } else if (to.name === 'paises') {
      next()
    } else if (to.name !== 'inicio') {
      next('/inicio')
    } else {
      next()
    }
  }
})

export default router
