<template>
  <div>
    <v-container>

      <!-- Formulario para insertar nuevos eventos -->
      <v-dialog
        v-model="dialog"
        fullscreen
        hide-overlay
        transition="dialog-bottom-transition"
      >

        <template v-slot:activator="{ on, attrs }">
          <v-btn
           color="primary"
           v-bind="attrs"
           v-on="on"
           title="Agregar un nuevo evento"
          >
          Agregar especie
          </v-btn>
        </template>

         <v-card>
          <v-toolbar
            dark
            color="primary"
          >
            <v-btn
              icon
              dark
              @click="dialog = false"
            >
              <v-icon>mdi-close</v-icon>
            </v-btn>
            <v-toolbar-title>Especies</v-toolbar-title>
            <v-spacer></v-spacer>
            <v-toolbar-items>
              <v-btn
                dark
                text
                @click="addEspecie"
              >
                Agregar
              </v-btn>
            </v-toolbar-items>
          </v-toolbar>

      <v-container>
      <v-row :no-gutters="width">

        <v-col
          cols="12"
          md="4"
          >
          <v-select
            label="Fase productiva"
            :items="fasesProd"
            v-model="esp.faseProd"
            item-value="valor"
            item-text="text"
            dense
            outlined
            filled
            >
          </v-select>
        </v-col>

        <v-col
          cols="12"
          md="4"
          >
        <v-select
          label="Especie forestal afectada"
          :items="espForestales"
          v-model="esp.espAfectada"
          item-value="valor"
          item-text="text"
          dense
          outlined
          filled
          >
        </v-select>
        </v-col>

        <v-col
          cols="12"
          md="4"
          >
          <v-select
            label="Especie extractiva"
            :items="espExtractivas"
            v-model="esp.espExtractiva"
            item-value="valor"
            item-text="text"
            dense
            outlined
            filled
            >
        </v-select>
        </v-col>

        <v-col
          cols="12"
          md="4"
          >
          <v-text-field
            label="Nombre común"
            v-model="esp.nombre"
            dense
            outlined
            filled
            >
          </v-text-field>
        </v-col>

        <v-col
          cols="12"
          md="4"
          >
        <v-select
          label="Objetivo de plantación"
          :items="espObjetivos"
          v-model="esp.objetivo"
          item-value="valor"
          item-text="text"
          dense
          outlined
          filled
          >
        </v-select>
        </v-col>

        <v-container v-if="esp.objetivo ==  1 || esp.objetivo ==  2 || esp.objetivo ==  4">

        <v-alert
          dense
          border="top"
          color="blue-grey"
          dark
        >
        Datos adicionales al objetivo
        </v-alert>

        <v-row
          class="mt-0"
          :no-gutters="width"
          >

          <v-col
            cols="12"
            md="4"
            sm="12"
            >
            <v-text-field
              label="No.arboles antes de la afectación"
              v-model="esp.noArbolesAntesAfectacion"
              dense
              outlined
              filled
              onkeypress="return (event.charCode >= 48 && event.charCode <= 57)"
              >
            </v-text-field>
          </v-col>

          <v-col
            cols="12"
            md="4"
            >
            <v-text-field
              label="No.entresacas o raleos a la fecha"
              v-model="esp.noEntresacas"
              dense
              outlined
              filled
              onkeypress="return (event.charCode >= 48 && event.charCode <= 57)"
              >
            </v-text-field>
          </v-col>

          <v-col
            cols="12"
            md="4"
            >
            <v-text-field
              label="Valor entresacas"
              v-model="esp.valEntreSacas"
              dense
              outlined
              filled
              onkeypress="return (event.charCode >= 48 && event.charCode <= 57)"
              >
            </v-text-field>
          </v-col>

          <v-col
            cols="12"
            md="4"
            >
            <v-text-field
              label="Porcentaje entresacas"
              v-model="esp.porceEntreSacas"
              dense
              outlined
              filled
              onkeypress="return (event.charCode >= 48 && event.charCode <= 57)"
              >
            </v-text-field>
          </v-col>

          <v-col
            cols="12"
            md="4"
            >
            <v-text-field
              label="Diámetro promedio a la altura del pecho (m)"
              v-model="esp.diametroPromedio"
              dense
              outlined
              filled
              onkeypress="return (event.charCode >= 48 && event.charCode <= 57)"
              >
            </v-text-field>
          </v-col>

          <v-col
            cols="12"
            md="4"
            >
            <v-text-field
              label="* Altura de comercial"
              v-model="esp.alturaComercial"
              dense
              outlined
              filled
              onkeypress="return (event.charCode >= 48 && event.charCode <= 57)"
              title="Es la altura aprovechable del arbol"
              >
            </v-text-field>
          </v-col>

          <v-col
            cols="12"
            md="4"
            >
            <v-text-field
              label="Altura total"
              v-model="esp.alturaTotal"
              dense
              outlined
              filled
              onkeypress="return (event.charCode >= 48 && event.charCode <= 57)"
              title="Es la altura de la base a la parte superior de la copa"
              >
            </v-text-field>
          </v-col>

          <v-col
            cols="12"
            md="4"
            >
            <v-text-field
              label="Turno de plantación en años"
              v-model="esp.plantacionAnos"
              dense
              outlined
              filled
              onkeypress="return (event.charCode >= 48 && event.charCode <= 57)"
              >
            </v-text-field>
          </v-col>

          <v-col
            cols="12"
            md="4"
            >
            <v-text-field
              label="Porcentaje de árboles al finalizar el turno"
              v-model="esp.porceArbolesTurnoFinal"
              dense
              outlined
              filled
              onkeypress="return (event.charCode >= 48 && event.charCode <= 57)"
              >
            </v-text-field>
          </v-col>

          <v-col
            cols="12"
            md="4"
            sm="12"
             v-if="esp.objetivo ==  1"
            >
            <v-text-field
              label="Volumen de madera afectado"
              v-model="esp.vlMaderaAfectado"
              dense
              outlined
              filled
              onkeypress="return (event.charCode >= 48 && event.charCode <= 57)"
              >
            </v-text-field>
          </v-col>

        </v-row>
        <v-divider></v-divider>
        <v-divider class="mb-2"></v-divider>
        </v-container>

        <v-menu
          v-model="esp.menuFecha"
          :close-on-content-click="false"
          :nudge-right="40"
          transition="scale-transition"
          offset-y
          min-width="auto"
          >
          <template v-slot:activator="{ on, attrs }">

            <v-col
              cols="12"
              md="4"
              >
              <v-text-field
                v-model="esp.fecha"
                label="Fecha establecimiento"
                readonly
                v-bind="attrs"
                v-on="on"
                dense
                outlined
                filled
              ></v-text-field>
            </v-col>

          </template>

          <v-col
            cols="12"
            md="4"
            >
            <v-date-picker
              v-model="esp.fecha"
              @input="esp.menuFecha = false"
              outlined
            ></v-date-picker>
          </v-col>

        </v-menu>

        <v-col
          cols="12"
          md="4"
          >
          <v-text-field
            label="Densidad siembra/hectarea"
            placeholder="arboles por hectarea"
            v-model="esp.densHectarea"
            dense
            outlined
            filled
            >
          </v-text-field>
        </v-col>

        <v-col
          cols="12"
          md="4"
          >
          <v-text-field
            label="Area sembrada/hectarea"
            v-model="esp.areaSembrada"
            dense
            outlined
            filled
            >
          </v-text-field>
        </v-col>

        <v-col
          cols="12"
          md="12"
          >
          <v-alert
            dense
            border="top"
            color="blue-grey"
            dark
          >
          Afectación
          </v-alert>
        </v-col>

        <v-col
          cols="12"
          md="4"
          >
          <v-text-field
            label="Área afectada en Hectareas"
            v-model="esp.areaAfectadaHectareas"
            dense
            outlined
            filled
            onkeypress="return (event.charCode >= 48 && event.charCode <= 57)"
            >
          </v-text-field>
        </v-col>

        <v-menu
          v-model="esp.menuFechaAfactaForestal"
          :close-on-content-click="false"
          :nudge-right="40"
          transition="scale-transition"
          offset-y
          min-width="auto"
          >
          <template v-slot:activator="{ on, attrs }">

            <v-col
              cols="12"
              md="4"
              >
              <v-text-field
                v-model="esp.fechaAfactaForestal"
                label="Fecha en el cual empezó a afectar el sistema forestal"
                readonly
                v-bind="attrs"
                v-on="on"
                dense
                outlined
                filled
              ></v-text-field>
            </v-col>

          </template>
          <v-col
            cols="12"
            md="4"
            >
            <v-date-picker
                v-model="esp.fechaAfactaForestal"
                @input="esp.menuFechaAfactaForestal = false"
                outlined
              ></v-date-picker>
          </v-col>
        </v-menu>

        <v-col
          cols="12"
          md="4"
          >
          <v-text-field
            label="Tiempo en días que afectó el sistema forestal"
            v-model="esp.diasAfectoSistemaForestal"
            dense
            outlined
            filled
            onkeypress="return (event.charCode >= 48 && event.charCode <= 57)"
            >
          </v-text-field>
        </v-col>

        <v-col
          cols="12"
          md="4"
          >
          <v-text-field
            label="Número de árboles afectados"
            v-model="esp.noArbolesAfectados"
            dense
            outlined
            filled
            onkeypress="return (event.charCode >= 48 && event.charCode <= 57)"
            >
          </v-text-field>
        </v-col>

        <v-col
          cols="12"
          md="4"
          >
          <v-text-field
            label="Valor que esperaba recibir al vender la producción afectada"
            v-model="esp.valorVenderProduccionAfectada"
            dense
            outlined
            filled
            onkeypress="return (event.charCode >= 48 && event.charCode <= 57)"
            >
          </v-text-field>
        </v-col>

       <v-col
          cols="12"
          md="12"
          >
          <v-switch
            v-model="esp.afectacionesEnMaquinaria"
            inset
            label="Afectaciones en maquinaria, equipo, insumos y herramienta actividad forestal"
          ></v-switch>
          <v-divider></v-divider>
        </v-col>

        <v-col
          cols="12"
          md="12"
          v-if="esp.afectacionesEnMaquinaria"
          >
          <v-select
            label="Tipo infraestructura"
            :items="tipoInfraestructura"
            v-model="esp.tipoInfraestructura"
            item-value="valor"
            item-text="text"
            dense
            outlined
            multiple
            filled
            >
          </v-select>
        </v-col>

       <!-- Semill -->
        <v-container v-if="semillas && esp.afectacionesEnMaquinaria">
          <v-alert
            dense
            border="top"
            color="blue-grey"
            dark
          >
          Semilla
          </v-alert>
        <v-row :no-gutters="width">
          <v-col
          cols="12"
          md="4"
          >
            <v-text-field
              label="Especie"
              v-model="semilla.especieSemilla"
              dense
              outlined
              filled
              >
            </v-text-field>
          </v-col>

          <v-col
          cols="12"
          md="4"
          >
            <v-select
              label="Tipo"
              :items="tipoSemilla"
              v-model="semilla.tipo"
              item-value="valor"
              item-text="text"
              dense
              outlined
              filled
              >
            </v-select>
          </v-col>

          <v-col
          cols="12"
          md="4"
          >
            <v-select
              label="Lotes de propagación"
              :items="lotePropagacionSemilla"
              v-model="semilla.lotePropagacionSemilla"
              item-value="valor"
              item-text="text"
              dense
              multiple
              outlined
              filled
              >
            </v-select>
          </v-col>

          <v-col
          cols="12"
          md="4"
          >
            <v-text-field
              label="Cantidad de semilla almacenada"
              v-model="semilla.cantidadAlmacenada"
              dense
              outlined
              filled
              onkeypress="return (event.charCode >= 48 && event.charCode <= 57)"
              >
            </v-text-field>
          </v-col>

          <v-col
          cols="12"
          md="4"
          >
            <v-select
              label="Unidad"
              :items="unidadSemilla"
              v-model="semilla.unidad"
              item-value="valor"
              item-text="text"
              dense
              outlined
              filled
              >
            </v-select>
          </v-col>

          <v-col
          cols="12"
          md="4"
          >
            <v-text-field
              label="Valor en pesos de la afectación"
              v-model="semilla.valorPesoAfectado"
              dense
              outlined
              filled
              onkeypress="return (event.charCode >= 48 && event.charCode <= 57)"
              >
            </v-text-field>
          </v-col>

        </v-row>
        </v-container>

        <!-- Fertilizante -->
        <v-container v-if="validarFertilizante && esp.afectacionesEnMaquinaria">
          <v-alert
            dense
            border="top"
            color="blue-grey"
            dark
          >
          Fertilizante
          </v-alert>
        <v-row :no-gutters="width">

          <v-col
          cols="12"
          md="4"
          >
            <v-select
              label="Tipo"
              :items="tipoFertilizante"
              v-model="fertilizante.tipoFertilizante"
              item-value="valor"
              item-text="text"
              dense
              outlined
              filled
              >
            </v-select>
          </v-col>

          <v-col
          cols="12"
          md="4"
          >
            <v-text-field
              label="Nombre"
              v-model="fertilizante.nombreFertilizante"
              dense
              outlined
              filled
              >
            </v-text-field>
          </v-col>

          <v-col
          cols="12"
          md="4"
          >
            <v-text-field
              label="Cantidad Kg o litros"
              v-model="fertilizante.cantidadKgLt"
              dense
              outlined
              filled
              onkeypress="return (event.charCode >= 48 && event.charCode <= 57)"
              >
            </v-text-field>
          </v-col>

          <v-col
          cols="12"
          md="4"
          >
            <v-text-field
              label="Valor en pesos de la afectación"
              v-model="fertilizante.valorPeso"
              dense
              outlined
              filled
              onkeypress="return (event.charCode >= 48 && event.charCode <= 57)"
              >
            </v-text-field>
          </v-col>

        </v-row>
        </v-container>

        <!-- Plaguicida -->
        <v-container v-if="validarplaguicida && esp.afectacionesEnMaquinaria">
          <v-alert
            dense
            border="top"
            color="blue-grey"
            dark
          >
          Plaguicida
          </v-alert>
        <v-row :no-gutters="width">

          <v-col
          cols="12"
          md="4"
          >
            <v-text-field
              label="Nombre"
              v-model="plaguicida.nombreFertilizante"
              dense
              outlined
              filled
              >
            </v-text-field>
          </v-col>

          <v-col
          cols="12"
          md="4"
          >
            <v-select
              label="Tipo"
              :items="listaPresentacion"
              v-model="plaguicida.listaPresentacion"
              item-value="valor"
              item-text="text"
              dense
              outlined
              filled
              >
            </v-select>
          </v-col>

          <v-col
          cols="12"
          md="4"
          >
            <v-text-field
              label="Cantidad"
              v-model="plaguicida.cantidad"
              dense
              outlined
              filled
              onkeypress="return (event.charCode >= 48 && event.charCode <= 57)"
              >
            </v-text-field>
          </v-col>

          <v-col
          cols="12"
          md="4"
          >
            <v-text-field
              label="Valor en pesos de la afectación"
              v-model="plaguicida.valorPeso"
              dense
              outlined
              filled
              onkeypress="return (event.charCode >= 48 && event.charCode <= 57)"
              >
            </v-text-field>
          </v-col>

        </v-row>
        </v-container>

        <!-- Instalaciones y maquinaria agricola -->
        <v-container v-if="validarMaquinaria && esp.afectacionesEnMaquinaria">
          <v-alert
            dense
            border="top"
            color="blue-grey"
            dark
          >
          Instalaciones y maquinaria agricola
          </v-alert>
        <v-row :no-gutters="width">

          <v-col
          cols="12"
          md="4"
          >
            <v-select
              label="Tipo"
              :items="tipoMaquinariaAgricola"
              v-model="maquinariaAgricola.tipoMaquinariaAgricola"
              item-value="valor"
              item-text="text"
              dense
              outlined
              filled
              >
            </v-select>
          </v-col>

          <v-col
          cols="12"
          md="4"
          >
            <v-text-field
              label="Nombre /marca comercial"
              v-model="maquinariaAgricola.nombreMarca"
              dense
              outlined
              filled
              >
            </v-text-field>
          </v-col>

          <v-col
          cols="12"
          md="4"
          >
            <v-text-field
              label="Valor en pesos de la afectación"
              v-model="maquinariaAgricola.valorPeso"
              dense
              outlined
              filled
              onkeypress="return (event.charCode >= 48 && event.charCode <= 57)"
              >
            </v-text-field>
          </v-col>

        </v-row>
        </v-container>

        <v-col
          cols="12"
          md="12"
          >
          <v-alert
            dense
            border="top"
            color="blue-grey"
            dark
          >
          Costos de producción forestal
          </v-alert>
        </v-col>

        <!-- Costos directos-->
        <v-col
          cols="12"
          md="12"
          >
          <v-alert
            dense
            border="left"
            color="blue-grey"
            dark
          >
          Costos Directos
          </v-alert>
        </v-col>

        <v-col
          cols="12"
          md="4"
          >
          <v-select
            label="Seleccionar rubro"
            :items="actividades"
            v-model="esp.actividad"
            item-value="valor"
            item-text="text"
            dense
            outlined
            filled
            >
          </v-select>
        </v-col>

        <v-col
        cols="12"
        md="4"
        >
          <v-text-field
            label="Costo generado en pesos colombianos"
            v-model="esp.costoGenerado"
            dense
            outlined
            filled
            onkeypress="return (event.charCode >= 48 && event.charCode <= 57)"
            >
          </v-text-field>
        </v-col>

        <v-btn color="error" @click="addCostoDirecto" style="width: 100%">
        Agregar costo
        </v-btn>

        <!-- Listado de costos directos -->
        <v-data-table
        :headers="costosDirectosHeader"
        :items="itemsCostosDirectos"
        class="elevation-1"
        style="width: 100%"
        >
          <template v-slot:top>
            <v-toolbar flat>
              <v-toolbar-title></v-toolbar-title>
            </v-toolbar>
          </template>
          <template v-slot:item.actions="{ item }">
            <!-- <v-icon
              small
              class="mr-2"
              @click="editProducto(item)"
            >
            mdi-pencil
            </v-icon> -->
            <v-icon
              small
              @click="deleteCostoDirecto(item)"
            >
            mdi-delete
            </v-icon>
          </template>
        </v-data-table>

        <!-- Costos indirectos-->
        <v-col
          cols="12"
          md="12"
          >
          <v-alert
            dense
            border="left"
            color="blue-grey"
            dark
          >
          Costos Indirectos
          </v-alert>
        </v-col>

         <v-col
          cols="12"
          md="4"
          >
          <v-select
            label="Seleccionar rubro"
            :items="rubros"
            v-model="esp.rubrosIndirectos"
            item-value="valor"
            item-text="text"
            dense
            outlined
            filled
            >
          </v-select>
        </v-col>

        <v-col
        cols="12"
        md="4"
        >
          <v-text-field
            label="Costo generado en pesos colombianos"
            v-model="esp.costoGeneradoIndirecto"
            dense
            outlined
            filled
            onkeypress="return (event.charCode >= 48 && event.charCode <= 57)"
            >
          </v-text-field>
        </v-col>

        <v-btn color="error" @click="addCostoIndirecto" style="width: 100%">
        Agregar costo
        </v-btn>

        <!-- Listado de costos directos -->
        <v-data-table
        :headers="costosIndirectosHeader"
        :items="itemsCostosIndirectos"
        class="elevation-1"
        style="width: 100%"
        >
          <template v-slot:top>
            <v-toolbar flat>
              <v-toolbar-title></v-toolbar-title>
            </v-toolbar>
          </template>
          <template v-slot:item.actions="{ item }">
            <!-- <v-icon
              small
              class="mr-2"
              @click="editProducto(item)"
            >
            mdi-pencil
            </v-icon> -->
            <v-icon
              small
              @click="deleteCostoIndirecto(item)"
            >
            mdi-delete
            </v-icon>
          </template>
        </v-data-table>
        <!-- <pre>{{$data.esp}}</pre> -->

      </v-row>
      <br>
      <br>
      </v-container>
      </v-card>

      </v-dialog>

      <!-- Listado de especies agregados al evento -->
      <v-data-table
        :headers="especiesHeaders"
        :items="especies"
        :expanded.sync="expanded"
        single-expand
        item-key="nombre"
        show-expand
        class="elevation-1"
      >
        <template v-slot:top>
          <v-toolbar flat>
            <v-toolbar-title></v-toolbar-title>
          </v-toolbar>
        </template>
        <template v-slot:item.actions="{ item }">
          <!-- <v-icon
            small
            class="mr-2"
            @click="editProducto(item)"
          >
          mdi-pencil
          </v-icon> -->
          <v-icon
            small
            @click="deleteEspecie(item)"
          >
          mdi-delete
          </v-icon>
        </template>
        <template v-slot:expanded-item="{ headers, item }">
          <td :colspan="headers.length">
            <v-row :no-gutters="width">
              <v-col
              cols="12"
              md="4"
              >
              <b>Fase Productiva:</b> {{ item.faseProd }}
              </v-col>
              <v-col
              cols="12"
              md="4"
              >
              <b>Especie Forestal:</b> {{ item.espAfectada }}
              </v-col>
              <v-col
              cols="12"
              md="4"
              >
              <b>Especie Extractiva:</b> {{ item.espExtractiva }}
              </v-col>
              <v-col
              cols="12"
              md="4"
              >
              <b>Fecha Establecimiento:</b> {{ item.fecha }}
              </v-col>
              <v-col
              cols="12"
              md="4"
              >
              <b>Densidad Siembra/Hectarea:</b> {{ item.densHectarea }}
              </v-col>
              <v-col
              cols="12"
              md="4"
              >
              <b>Area Sembrada/Hectarea:</b> {{ item.areaSembrada }}
              </v-col>
            </v-row>
          </td>
        </template>
      </v-data-table>

    </v-container>
  </div>
</template>
<script>

import { newEvent } from '../../services/api.js'
import jwtDecode from 'jwt-decode'

export default {
  name: 'dataEspecie',
  props: ['obtenerDataEspecie'],
  data () {
    return {

      // variables del sistema
      e6: 3,
      tab: 0,
      mfnac: false,
      esp: {},
      semilla: {},
      fertilizante: {},
      plaguicida: {},
      maquinariaAgricola: {},
      fasesProd: [],
      espForestales: [],
      espExtractivas: [],
      espObjetivos: [],
      tipoInfraestructura: [],
      tipoSemilla: [],
      lotePropagacionSemilla: [],
      unidadSemilla: [],
      tipoFertilizante: [],
      listaPresentacion: [],
      tipoMaquinariaAgricola: [],
      rubros: [],
      actividades: [],

      // variables de componentes
      // modal agregar nuevo productor
      dialog: false,
      notifications: false,
      sound: true,
      widgets: false,
      // tabla listado de productores agregados al evento
      expanded: [],
      especiesHeaders: [
        {
          text: 'Especie',
          align: 'start',
          sortable: false,
          value: 'nombre'
        },
        { text: 'Eliminar', value: 'actions', sortable: false, width: '2%' },
        { text: 'Ver más', value: 'data-table-expand', width: '2%' }
      ],
      especies: [],
      // tabla costos generados directos
      itemsCostosDirectos: [],
      costosDirectosHeader: [
        {
          text: 'Id',
          align: 'start',
          sortable: false,
          value: 'id'
        },
        {
          text: 'Rubro',
          align: 'start',
          sortable: false,
          value: 'rubros'
        },
        {
          text: 'Costo',
          align: 'start',
          sortable: false,
          value: 'costo'
        },
        { text: 'Eliminar', value: 'actions', sortable: false, width: '2%' }
      ],
      // tabla costos generados indirectos,
      itemsCostosIndirectos: [],
      costosIndirectosHeader: [
        {
          text: 'Id',
          align: 'start',
          sortable: false,
          value: 'id'
        },
        {
          text: 'Rubro',
          align: 'start',
          sortable: false,
          value: 'rubros'
        },
        {
          text: 'Costo',
          align: 'start',
          sortable: false,
          value: 'costo'
        },
        { text: 'Eliminar', value: 'actions', sortable: false, width: '2%' }
      ]
    }
  },
  async mounted () {
    await this.getDataEventSpecie()
  },
  computed: {
    width () {
      let status

      switch (this.$vuetify.breakpoint.name) {
        case 'xs':
          status = true
          break
        case 'sm':
          status = true
          break
        case 'md':
          status = false
          break
        case 'lg':
          status = false
          break
        case 'xl':
          status = false
          break
      }

      return status
    },
    semillas () {
      for (var item in this.esp.tipoInfraestructura) {
        if (this.esp.tipoInfraestructura[item] === 1) {
          return true
        }
      }
      return false
    },
    validarFertilizante () {
      for (var item in this.esp.tipoInfraestructura) {
        if (this.esp.tipoInfraestructura[item] === 2) {
          return true
        }
      }
      return false
    },
    validarplaguicida () {
      for (var item in this.esp.tipoInfraestructura) {
        if (this.esp.tipoInfraestructura[item] === 3) {
          return true
        }
      }
      return false
    },
    validarMaquinaria () {
      for (var item in this.esp.tipoInfraestructura) {
        if (this.esp.tipoInfraestructura[item] === 4) {
          return true
        }
      }
      return false
    }
  },
  methods: {
    addEspecie () {
      this.dialog = false
      if (Object.entries(this.esp).length === 0) {
        return
      }
      this.esp.costosDirectos = this.itemsCostosDirectos
      this.esp.costosInDirectos = this.itemsCostosIndirectos
      this.esp.semilla = this.semilla
      this.esp.fertilizante = this.fertilizante
      this.esp.plaguicida = this.plaguicida
      this.esp.maquinariaAgricola = this.maquinariaAgricola
      this.especies.push(this.esp)
      this.esp = {}
      this.obtenerDataEspecie(this.especies)
    },
    deleteEspecie (item) {
      const editedIndex = this.especies.indexOf(item)
      this.especies.splice(editedIndex, 1)
      this.obtenerDataEspecie(this.especies)
    },
    // editProducto(item) {
    //   this.dialog = true
    //   this.editedIndex = this.desserts.indexOf(item)
    //   this.editedItem = Object.assign({}, item)
    //   this.dialog = true
    // },
    deleteCostoDirecto (item) {
      const editedIndexCostoDirecto = this.itemsCostosDirectos.indexOf(item)
      this.itemsCostosDirectos.splice(editedIndexCostoDirecto, 1)
    },
    addCostoDirecto () {
      if (!this.esp.costoGenerado) {
        return
      }
      if (!this.esp.actividad) {
        return
      }

      const newCostoDirecto = {}
      newCostoDirecto.costo = this.esp.costoGenerado
      newCostoDirecto.id = this.esp.actividad
      var text = this.actividades.filter(actividad => actividad.valor === this.esp.actividad)
      newCostoDirecto.rubros = text[0].text
      this.itemsCostosDirectos.push(newCostoDirecto)
      this.esp.costoGenerado = ''
      this.esp.actividad = {}
    },
    deleteCostoIndirecto (item) {
      const editedIndexCostoIndirecto = this.itemsCostosIndirectos.indexOf(item)
      this.itemsCostosIndirectos.splice(editedIndexCostoIndirecto, 1)
    },
    addCostoIndirecto () {
      if (!this.esp.costoGeneradoIndirecto) {
        return
      }
      if (!this.esp.rubrosIndirectos) {
        return
      }
      const newCostoIndirecto = {}
      newCostoIndirecto.costo = this.esp.costoGeneradoIndirecto
      newCostoIndirecto.id = this.esp.rubrosIndirectos
      var text = this.rubros.filter(rubro => rubro.valor === this.esp.rubrosIndirectos)
      newCostoIndirecto.rubros = text[0].text
      this.itemsCostosIndirectos.push(newCostoIndirecto)
      this.esp.costoGeneradoIndirecto = ''
      this.esp.rubrosIndirectos = {}
    },
    async getDataEventSpecie () {
      try {
        const res = await newEvent.getDataEventSpecie()

        if (res.status === 200) {
          const encoded = res.data.token
          const decoded = jwtDecode(encoded)
          const data = decoded.dataEventSpecie

          for (const documento in data.faseProductiva) {
            var addFase = {
              valor: data.faseProductiva[documento][0],
              text: data.faseProductiva[documento][1]
            }
            this.fasesProd.push(addFase)
          }

          for (const documento in data.especieForestal) {
            var addEspecieFor = {
              valor: data.especieForestal[documento][0],
              text: data.especieForestal[documento][1]
            }
            this.espForestales.push(addEspecieFor)
          }

          for (const documento in data.especieExtractiva) {
            var addEspecieEx = {
              valor: data.especieExtractiva[documento][0],
              text: data.especieExtractiva[documento][1]
            }
            this.espExtractivas.push(addEspecieEx)
          }

          for (const documento in data.objePlantacion) {
            var addObje = {
              valor: data.objePlantacion[documento][0],
              text: data.objePlantacion[documento][1]
            }
            this.espObjetivos.push(addObje)
          }

          for (const documento in data.tipoInfraestrcutura) {
            var addInfra = {
              valor: data.tipoInfraestrcutura[documento][0],
              text: data.tipoInfraestrcutura[documento][1]
            }
            this.tipoInfraestructura.push(addInfra)
          }

          for (const documento in data.tipoSemilla) {
            var addSemilla = {
              valor: data.tipoSemilla[documento][0],
              text: data.tipoSemilla[documento][1]
            }
            this.tipoSemilla.push(addSemilla)
          }

          for (const documento in data.lotePropaga) {
            var addLoteP = {
              valor: data.lotePropaga[documento][0],
              text: data.lotePropaga[documento][1]
            }
            this.lotePropagacionSemilla.push(addLoteP)
          }

          for (const documento in data.unidad) {
            var addUnidad = {
              valor: data.unidad[documento][0],
              text: data.unidad[documento][1]
            }
            this.unidadSemilla.push(addUnidad)
          }

          for (const documento in data.tipoFertilizante) {
            var addFertilizante = {
              valor: data.tipoFertilizante[documento][0],
              text: data.tipoFertilizante[documento][1]
            }
            this.tipoFertilizante.push(addFertilizante)
          }

          for (const documento in data.tipoPlaguicida) {
            var addPlaguicida = {
              valor: data.tipoPlaguicida[documento][0],
              text: data.tipoPlaguicida[documento][1]
            }
            this.listaPresentacion.push(addPlaguicida)
          }

          for (const documento in data.tipoMaquinaria) {
            var addMaquinaria = {
              valor: data.tipoMaquinaria[documento][0],
              text: data.tipoMaquinaria[documento][1]
            }
            this.tipoMaquinariaAgricola.push(addMaquinaria)
          }

          for (const documento in data.rubros) {
            var addRubros = {
              valor: data.rubros[documento][0],
              text: data.rubros[documento][1]
            }
            this.rubros.push(addRubros)
          }

          for (const documento in data.actividad) {
            var addActividad = {
              valor: data.actividad[documento][0],
              text: data.actividad[documento][1]
            }
            this.actividades.push(addActividad)
          }
        }
      } catch (error) {
        console.log(error)
      }
    }
  }
}
</script>
