<template>
<v-container class="mt-5">
    <v-footer
      v-bind="localAttrs"
      :padless="padless"
      class="mt-5"
      elevation="2"
    >
      <v-card
        flat
        tile
        width="100%"
        class="blue darken-2 text-center"
      >
        <v-card-text>
         <v-row justify="center">
            <v-img
            max-height="50"
            max-width="50"
            src="fao_logo_white.png"
            ></v-img>
         </v-row>
        </v-card-text>

        <!-- <v-divider></v-divider> -->
<!--
        <v-card-text class="white--text">
          {{ new Date().getFullYear() }} — <strong>FAO</strong>
        </v-card-text> -->
      </v-card>
    </v-footer>

</v-container>
</template>
<script>
export default {
  data: () => ({
    icons: [
      'mdi-home',
      'mdi-email',
      'mdi-calendar',
      'mdi-delete'
    ],
    padless: true,
    variant: 'fixed'
  }),
  computed: {
    localAttrs () {
      const attrs = {}

      if (this.variant === 'default') {
        attrs.absolute = false
        attrs.fixed = false
      } else {
        attrs[this.variant] = true
      }
      return attrs
    }
  }
}
</script>
