<template>
  <div
    class="overflow-hidden mb-5"
  >
    <v-app-bar
      absolute
      dark
      color="primary"
      elevate-on-scroll
      scroll-target="#scrolling-techniques-7"
      >
      <v-app-bar-nav-icon @click="drawer = true"></v-app-bar-nav-icon>

      <v-toolbar-title>{{ currentRouteName }}</v-toolbar-title>

      <v-spacer></v-spacer>

      <v-offline @detected-condition="amIOnline">

        <div slot="online">
          <v-btn icon v-if="onLine">
            <v-icon>mdi-wifi-strength-outline</v-icon>
          </v-btn>
           <v-btn icon v-if="!onLine">
            <v-icon>mdi-wifi-strength-off-outline</v-icon>
          </v-btn>
        </div>

        <div slot="offline">
        </div>

      </v-offline>

    </v-app-bar>

    <v-navigation-drawer
      v-model="drawer"
      absolute
      temporary
    >
      <v-list
        nav
        dense
      >
      <v-list-item two-line :class="miniVariant && 'px-0'">
        <v-list-item-avatar>
          <v-icon size="50">mdi-account-circle</v-icon>
        </v-list-item-avatar>

        <v-list-item-content>
          <v-list-item-title>{{ this.$session.get('usuario') }}</v-list-item-title>
          <v-list-item-subtitle>{{ this.$session.get('email') }}</v-list-item-subtitle>
        </v-list-item-content>
      </v-list-item>

      <v-divider></v-divider>

        <v-list-item-group
          v-model="group"
          active-class="blue--text text--accent-4"
        >
          <v-list-item  @click="$router.push('/logout')">
            <v-list-item-icon>
              <v-icon>mdi-logout-variant</v-icon>
            </v-list-item-icon>
            <v-list-item-title>Cerrar sesión</v-list-item-title>
          </v-list-item>

          <v-list-item @click="routeHome()">
            <v-list-item-icon>
              <v-icon>mdi-home</v-icon>
            </v-list-item-icon>
            <v-list-item-title>Home</v-list-item-title>
          </v-list-item>

          <v-list-item @click="routeUser()">
            <v-list-item-icon>
              <v-icon>mdi-account-cog</v-icon>
            </v-list-item-icon>
            <v-list-item-title>Datos de usuario</v-list-item-title>
          </v-list-item>

          <v-list-item @click="routeNewEvento()">
            <v-list-item-icon>
              <v-icon>mdi-shape-square-rounded-plus</v-icon>
            </v-list-item-icon>
            <v-list-item-title>Nuevo evento</v-list-item-title>
          </v-list-item>
        </v-list-item-group>
      </v-list>
    </v-navigation-drawer>
  </div>
</template>
<script>
import VOffline from 'v-offline'

export default {
  name: 'menuApp',
  components: { VOffline },
  data () {
    return {
      drawer: false,
      group: null,
      miniVariant: false,
      onLine: true,
      loading: true
    }
  },
  created: function () {
    // if (this.$route.name != 'Seguimiento') {
    //     this.$router.push('/updateUser');
    // }
  },
  methods: {
    amIOnline (e) {
      this.onLine = e
    },
    routeHome () {
      if (this.$route.name !== 'listadoEventos') {
        this.$router.push('/home')
      }
    },
    routeUser () {
      if (this.$route.name !== 'editarUsuario') {
        this.$router.push('/updateUser')
      }
    },
    routeNewEvento () {
      if (this.$route.name !== 'nuevoEvento') {
        console.log('acá')
        this.$router.push('/newEvento')
      }
    }
  },
  computed: {
    currentRouteName () {
      const name = this.$route.name
      var nombreModulo = ''
      var inicio = 0
      var fin = 0

      for (var index = 0; index <= name.length; index++) {
        var letraActual = name.charAt(index)

        if (letraActual === letraActual.toUpperCase()) {
          fin = index
          nombreModulo = nombreModulo + ' ' + name.substring(inicio, fin)
          inicio = fin
        }
      }

      return nombreModulo.toUpperCase()
    }
  }
}
</script>
