import axios from 'axios'
const user = {
  loggin: params => axios.post('login', params),
  createUser: params => axios.post('createUser', params),
  updateUser: params => axios.post('updateUser', params),
  storeSolicitud: params => axios.post('solicitud', params),
  getSolicitudes: () => axios.get('solicitudes'),
  approve: params => axios.post('approve', params),
  reject: params => axios.post('reject', params),
  getUsers: () => axios.get('users'),
  editUser: params => axios.put('user', params),
  changePassword: params => axios.post('changePassword', params),
  recoverPassword: params => axios.post('recoverPassword', params)
}
const eventosCreados = {
  getEventos: () => axios.get('getEventos')
}
const newEvent = {
  getDataProductor: params => axios.get('dataProductor', params),
  getDataMapa: params => axios.get('dataMapa', params),
  getDataEvent: params => axios.get('dataEvento', params),
  getDataEventSpecie: params => axios.get('dataEventoEspecie', params),
  setDataEvento: params => axios.post('guardarEvento', params)
}
const seguimientoEvento = {
  getSeguimiento: params => axios.post('getSeguimiento', params),
  setSeguimiento: params => axios.post('guardarSeguimiento', params, {
    headers: {
      'Content-Type': 'multipart/form-data'
    }
  }),
  getAdjunto: params => axios.post('getAdjunto', params, { responseType: 'blob' })
}
const informesEvento = {
  setDataInformesEvento: params => axios.post('setDataInformesEvento', params)
}

export {
  user,
  eventosCreados,
  newEvent,
  seguimientoEvento,
  informesEvento
}
