<template>

<!--  Modal pantalla completa -->
  <v-dialog
    v-model="dialog"
    fullscreen
    hide-overlay
    scrollable
    transition="dialog-bottom-transition"
  >

    <!--  Botón para desplegar el modal pantalla completa -->
    <template v-slot:activator="{ on, attrs }">
      <v-col>
        <v-btn
         color="error"
         v-bind="attrs"
         v-on="on"
         title="Abrir mapa"
         block
         @click="reloadMap()"
        >
        <v-icon>mdi-google-maps</v-icon>Asignar ubicación del evento
        </v-btn>
      </v-col>
    </template>

    <v-card>
      <v-toolbar
        dark
        color="primary"
      >
        <v-btn
          icon
          dark
          @click="dialog = false"
        >
          <v-icon>mdi-close</v-icon>
        </v-btn>
        <v-toolbar-title>Ubicación del evento</v-toolbar-title>
        <v-spacer></v-spacer>
        <v-toolbar-items>
          <v-btn
            dark
            text
            @click="guardarDataMapa()"
          >
            Guardar
          </v-btn>
        </v-toolbar-items>
      </v-toolbar>

    <!-- <div class="info" style="height: 15%">
      <span>Center: {{ center }}</span>
      <span>Zoom: {{ zoom }}</span>
      <span>Bounds: {{ bounds }}</span>
    </div> -->

      <!--  mapa -->
      <div style="display: block;overflow: auto; height:100vh;">
          <l-map
            ref="mapEvent"
            :zoom="zoom"
            :center="center"
            @update:center="centerUpdated"
            @update:bounds="boundsUpdated"
            style="height: 100%;"
            v-if="mapSee"
          >

            <!--  Complementos del mapa -->
            <!-- <l-tile-layer :url="url"></l-tile-layer> -->
            <l-control-layers position="topright"  ></l-control-layers>
            <l-tile-layer
              v-for="tileProvider in tileProviders"
              :key="tileProvider.name"
              :name="tileProvider.name"
              :visible="tileProvider.visible"
              :url="tileProvider.url"
              :attribution="tileProvider.attribution"
              layer-type="base"/>

            <l-control-scale position="topright" :imperial="true" :metric="false"></l-control-scale>

            <vue-leaflet-minimap
              ref="miniMap"
              :layer="layer"
              :options="optionsMiniMap"
            ></vue-leaflet-minimap>

            <l-control-fullscreen position="topleft"
                :options="{ title: { 'false': 'Ampliar', 'true': 'Reducir' } }"
            />

            <!--  Icono de ubicación -->
            <l-marker
            v-for="marker in markers"
            :key="marker.id"
            :visible="marker.visible"
            :draggable="marker.draggable"
            :lat-lng="marker.position"
            @dragend="setMarkerPosition"
            >
            <l-icon
              :icon-anchor="staticAnchor"
              class-name="someExtraClass">
             <v-icon
             x-large
             color="red"> mdi-map-marker </v-icon>
              </l-icon>
            </l-marker>

            <!--  Formulario mapa -->
            <l-control
            position="bottomleft">

              <!-- <v-container> -->
                <v-row>
                  <!-- Boton para optener la ubicación -->
                  <v-col
                  cols="12"
                  md="12"
                  >
                    <v-btn
                      style="background-color:white;"
                      elevation="3"
                      color="primary"
                      icon
                      dark
                      @click="pedirUbicacion"
                    >
                      <v-icon> mdi-crosshairs-gps </v-icon>
                    </v-btn>
                  </v-col>
                  <!--  Boton para activar el formulario mapa -->
                  <v-col
                  cols="12"
                  md="12"
                  >
                    <v-btn
                      style="background-color:white;"
                      elevation="3"
                      color="primary"
                      icon
                      dark
                      @click="formularioUbicacion"
                    >
                      <v-icon> mdi-form-select </v-icon>
                    </v-btn>
                  </v-col>

                </v-row>
              <!-- </v-container> -->

            </l-control>

          </l-map>
      </div>

      <v-dialog
        v-model="dialogMenu"
        max-width="900px"
      >
        <!--  Formulario mapa -->
        <v-container>
          <v-card
          elevation="2"
          >
            <v-card-text>
              <p class="text--primary">
                Por favor situé en el mapa el sitio de ocurrencia.
              </p>
              <hr>
                <v-container>
                  <v-form
                  lazy-validation
                  ref="form"
                  v-model="valid"
                  >
                    <v-row :no-gutters="width">

                      <v-col
                      cols="12"
                      md="12"
                      >
                        <v-file-input
                          label="Shapefile o Kml"
                          v-model="archivoUbicacion"
                          dense
                          outlined
                          filled
                          >
                        </v-file-input>
                      </v-col>

                      <v-col
                      cols="12"
                      md="12"
                      >
                        <v-text-field
                          label="Nombre puerto desembarque"
                          v-model="ubicacion.nombrePuerto"
                          dense
                          outlined
                          filled
                          >
                        </v-text-field>
                      </v-col>

                      <v-col
                        cols="12"
                        md="12"
                        >
                          <v-switch
                            v-model="eventoVereda"
                            inset
                            label="Evento ubicado en vereda"
                          ></v-switch>
                      </v-col>

                      <v-col
                      cols="12"
                      md="4"
                      >
                        <v-text-field
                          label="Latitud(X.Y)º"
                          v-model="ubicacion.latitud"
                          dense
                          outlined
                          filled
                          required
                          :rules="documentRules"
                          >
                        </v-text-field>
                      </v-col>

                      <v-col
                      cols="12"
                      md="4"
                      >
                        <v-text-field
                          label="Longitud(X.Y)º"
                          v-model="ubicacion.longitud"
                          dense
                          outlined
                          filled
                          required
                          :rules="documentRules"
                          >
                        </v-text-field>
                      </v-col>

                      <v-col
                      cols="12"
                      md="4"
                      >
                        <v-text-field
                          label="Precisión(m)"
                          v-model="ubicacion.precision"
                          dense
                          outlined
                          filled
                          >
                        </v-text-field>
                      </v-col>

                      <v-col
                      cols="12"
                      md="4"
                      >
                        <v-text-field
                          label="Altitud(m)"
                          v-model="ubicacion.altitud"
                          dense
                          outlined
                          filled
                          >
                        </v-text-field>
                      </v-col>

                      <v-col
                        cols="12"
                        md="4"
                        >
                        <v-select
                          label="Departamento"
                          :items="departamentos"
                          v-model="ubicacion.departamento"
                          item-value="valor"
                          dense
                          outlined
                          filled
                          required
                          :rules="documentRules"
                          @change="cargarMunicipioSegunDepartamento()"
                          >
                        </v-select>
                      </v-col>

                      <v-col
                        cols="12"
                        md="4"
                        >
                        <v-select
                          label="Municipio"
                          :items="relMunicipioDepartamento"
                          v-model="ubicacion.municipio"
                          item-value="valor"
                          dense
                          outlined
                          filled
                          required
                          :rules="documentRules"
                          @change="cargarVeredaSegunMunicipio()"
                          >
                        </v-select>
                      </v-col>

                      <v-col
                        cols="12"
                        md="4"
                        >
                        <v-select
                          label="Veredas"
                          :items="relVeredaMunicipio"
                          v-model="ubicacion.codVereda"
                          item-value="valor"
                          dense
                          outlined
                          filled
                          v-if="eventoVereda"
                          >
                        </v-select>
                      </v-col>

                      <v-col
                      cols="12"
                      md="12"
                      >
                        <v-textarea
                          label="Observación"
                          v-model="ubicacion.observacion"
                          dense
                          outlined
                          filled
                          required
                          :rules="documentRules"
                          >
                        </v-textarea>
                      </v-col>

                    </v-row>

                  </v-form>

                </v-container>

            </v-card-text>
            <v-card-actions class="justify-end">
              <v-btn
                text
                @click="dialogMenu = false"
            >Cerrar</v-btn>
            </v-card-actions>
          </v-card>
        </v-container>
      </v-dialog>

    </v-card>
  </v-dialog>
</template>

<script>

import { newEvent } from '../../services/api.js'
import jwtDecode from 'jwt-decode'

import L, { latLng } from 'leaflet'

import { LMap, LTileLayer, LMarker, LIcon, LControl, LControlLayers, LControlScale } from 'vue2-leaflet'
import LControlFullscreen from 'vue2-leaflet-fullscreen'
import VueLeafletMinimap from 'vue-leaflet-minimap'
import 'leaflet/dist/leaflet.css'
import 'leaflet-minimap/dist/Control.MiniMap.min.css'

export default {
  name: 'EventoUbicacion',
  components: {
    LMap,
    LTileLayer,
    LMarker,
    LIcon,
    LControl,
    LControlLayers,
    LControlScale,
    LControlFullscreen,
    VueLeafletMinimap
  },
  props: ['obtenerDataMapa'],
  data () {
    return {
      // Vairables necesarias para el funcionamiento del mapa.
      dialog: false,
      dialogMenu: false,
      bounds: null,
      staticAnchor: [20, 40],
      markers: [
        {
          id: 'm1',
          position: { lat: 7.086594479396249, lng: -73.13499631331399 },
          tooltip: 'Ubicación actual',
          draggable: true,
          visible: true
        }
      ],
      zoom: 20,
      center: latLng(7.086594479396249, -73.13499631331399),
      url: 'https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png',
      tileProviders: [
        {
          name: 'OpenStreetMap',
          visible: true,
          url: 'https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png'
        },
        {
          name: 'OpenTopoMap',
          visible: false,
          url: 'https://{s}.tile.opentopomap.org/{z}/{x}/{y}.png'
        }
      ],
      layer: new L.TileLayer('http://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png'),
      optionsMiniMap: {
        position: 'bottomright',
        width: 150,
        height: 125,
        toggleDisplay: true
      },

      // Reglas
      documentRules: [
        v => !!v || 'Campo obligatorio'
      ],

      // Variables del formulario de ubicación.
      valid: false,
      eventoVereda: false,
      departamentos: [],
      municipios: [],
      relVeredaMunicipio: [],
      veredas: [],
      relMunicipioDepartamento: [],
      ubicacion: {
        departamento: '',
        municipio: '',
        latitud: '',
        longitud: '',
        precision: '',
        altitud: '',
        enVereda: '',
        codVereda: '',
        nombrePuerto: '',
        observacion: ''
      },
      archivoUbicacion: [],
      map: [],
      mapSee: false
    }
  },
  async mounted () {
    await this.getDataMapa()
  },
  created: function () {
    if ('geolocation' in navigator) {
      navigator.geolocation.getCurrentPosition(

        ubicacion => {
          const coordenadas = ubicacion.coords

          this.markers[0].position.lat = 0
          this.markers[0].position.lng = 0
          this.center.lat = 0
          this.center.lng = 0

          this.center.lat = coordenadas.latitude
          this.center.lng = coordenadas.longitude
          this.markers[0].position.lat = coordenadas.latitude
          this.markers[0].position.lng = coordenadas.longitude
          this.ubicacion.latitud = coordenadas.latitude
          this.ubicacion.longitud = coordenadas.longitude
        },
        () => {
          this.$toasted.error('No se pudo obtener su ubicación, intentelo mas tarde.', { duration: 5000 })
        },
        {
          enableHighAccuracy: true
        }
      )
    } else this.$toasted.error('Lo sentimos, su navegador no tiene soporte para obtener su ubicación.', { duration: 5000 })
  },
  methods: {
    async getDataMapa () {
      try {
        const res = await newEvent.getDataMapa()

        if (res.status === 200) {
          const encoded = res.data.token
          const decoded = jwtDecode(encoded)
          const data = decoded.dataMapa

          for (const itemDepa in data.departamentos) {
            var addDepartamento = {
              valor: data.departamentos[itemDepa][0],
              text: data.departamentos[itemDepa][1]
            }
            this.departamentos.push(addDepartamento)
          }

          for (const itemMuni in data.municipios) {
            var addMunicipio = {
              valor: data.municipios[itemMuni][0],
              text: data.municipios[itemMuni][2],
              idDepartamento: data.municipios[itemMuni][3]
            }
            this.municipios.push(addMunicipio)
          }

          for (const itemVereda in data.veredas) {
            var addVereda = {
              valor: data.veredas[itemVereda][0],
              text: data.veredas[itemVereda][1],
              idMunicipio: data.veredas[itemVereda][2]
            }
            this.veredas.push(addVereda)
          }
        }
      } catch (error) {
        console.log(error)
      }
    },
    cargarMunicipioSegunDepartamento () {
      this.relMunicipioDepartamento = this.municipios.filter(municipio => municipio.idDepartamento === this.ubicacion.departamento)
    },
    cargarVeredaSegunMunicipio () {
      this.relVeredaMunicipio = this.veredas.filter(vereda => vereda.idMunicipio === this.ubicacion.municipio)
    },
    formularioUbicacion () {
      this.dialogMenu = !this.dialogMenu
      this.valid = true
    },
    centerUpdated (center) {
      this.center = center
    },
    boundsUpdated (bounds) {
      this.bounds = bounds
    },
    pedirUbicacion () {
      if ('geolocation' in navigator) {
        navigator.geolocation.getCurrentPosition(

          ubicacion => {
            const coordenadas = ubicacion.coords
            this.markers[0].position.lat = 0
            this.markers[0].position.lng = 0
            this.center.lat = 0
            this.center.lng = 0

            this.center.lat = coordenadas.latitude
            this.center.lng = coordenadas.longitude
            this.markers[0].position.lat = coordenadas.latitude
            this.markers[0].position.lng = coordenadas.longitude
            this.ubicacion.latitud = coordenadas.latitude
            this.ubicacion.longitud = coordenadas.longitude
          },
          () => {
            this.$toasted.error('No se pudo obtener su ubicación, intentelo mas tarde.', { duration: 5000 })
          },
          {
            enableHighAccuracy: true
          }
        )
      } else this.$toasted.error('Lo sentimos, su navegador no tiene soporte para obtener su ubicación.', { duration: 5000 })
    },
    setPos (e) {
      const layer = e.target
      const ll = layer.getLatLng()
      layer.setLatLng(ll)
      return [ll.lat, ll.lng]
    },
    setMarkerPosition (e) {
      const position = this.setPos(e)
      this.markers[0].position.lat = position[0]
      this.markers[0].position.lng = position[1]
      this.ubicacion.latitud = this.markers[0].position.lat
      this.ubicacion.longitud = this.markers[0].position.lng
    },
    reloadMap () {
      this.mapSee = true
      this.$nextTick(() => {
        const mapEvent = this.$refs.mapEvent
        // const miniMap = this.$refs.miniMap
        setTimeout(function () {
          mapEvent.mapObject.invalidateSize()
        }, 250)
      })
    },
    guardarDataMapa () {
      if (!this.valid) {
        this.$toasted.error('Para grabar la ubicación primero debe diligenciar el formulario.', { duration: 5000 })
        return
      }

      if (this.eventoVereda && !this.ubicacion.codVereda) {
        this.$toasted.error('Si el evento es en una vereda se debe seleccionar la vereda.', { duration: 5000 })
        return
      }
      if (this.$refs.form.validate()) {
        this.dialog = false
        this.obtenerDataMapa(this.ubicacion)
      } else {
        this.$toasted.error('Hay errores en el registro de los datos de la ubicación.', { duration: 5000 })
      }
    }
  },
  computed: {
    options () {
      return {
        onEachFeature: this.onEachFeatureFunction
      }
    },
    width () {
      let status

      switch (this.$vuetify.breakpoint.name) {
        case 'xs':
          status = true
          break
        case 'sm':
          status = true
          break
        case 'md':
          status = false
          break
        case 'lg':
          status = false
          break
        case 'xl':
          status = false
          break
      }

      return status
    }
  }
}
</script>

<style>
</style>
