<template>
  <div>

    <v-container class="mt-5">
    <v-btn
     class="mt-5"
     color="error"
     title="Guardar Evento"
     block
     @click="guardarEvento()"
    >
      Guardar evento
    </v-btn>
    <v-progress-linear
      class="mt-1"
      :active="loading"
      :indeterminate="loading"
      color="green accent-4"
      rounded
      height="6"
    ></v-progress-linear>

    <v-stepper
      v-model="e6"
      vertical
      non-linear
      style="box-shadow: none !important"
      >
      <v-stepper-step
        :complete="e6 > 1"
        step="1"
        editable
        >
        Información del productor
      </v-stepper-step>

      <v-stepper-content step="1">
        <dataProveedor :obtenerData="onResultadosProductor"></dataProveedor>
      </v-stepper-content>

      <v-stepper-step
        :complete="e6 > 2"
        step="2"
        editable
        >
        Ubicación del evento
      </v-stepper-step>

      <v-stepper-content step="2">
        <v-container class="">
          <mapaEvento :obtenerDataMapa="onResultadosMapa"></mapaEvento>
        </v-container>
      </v-stepper-content>

      <v-stepper-step
        :complete="e6 > 3"
        editable
        step="3">
        Detalle del evento
      </v-stepper-step>
      <v-stepper-content step="3">

        <v-container  ref="formEvent">
        <v-row :no-gutters="width">
          <v-col
          cols="12"
          md="4"
          >
            <v-select
              label="Tipo de evento"
              :items="tiposEvento"
              item-value="valor"
              v-model="selEvento.tipoEv"
              dense
              outlined
              filled
              :rules="rulesEventGeneral"
              required
              @change="cargarSubEventoSegunEvento()"
              >
            </v-select>
          </v-col>

          <v-col
          cols="12"
          md="4"
          >
          <v-select
            label="Tipo de subevento"
            :items="relEventoSubEvento"
            item-value="valor"
            v-model="selEvento.subEv"
            dense
            outlined
            filled
            :rules="rulesEventGeneral"
            required
            >
          </v-select>
          </v-col>

          <v-col
          cols="12"
          md="4"
          >
            <v-select
              label="Sistema productivo afectado"
              :items="sisProds"
              item-value="valor"
              item-text="text"
              v-model="selEvento.sisProds"
              dense
              outlined
              multiple
              filled
              :rules="rulesEventGeneral"
              required
              >
            </v-select>
          </v-col>
        </v-row>
        </v-container>
        <especies  v-if="contForestal" :obtenerDataEspecie="onResultadosEspecie"></especies>
      </v-stepper-content>

    </v-stepper>
    <!-- <pre>{{$data.selEvento}}</pre> -->
    </v-container>
  </div>
</template>
<script>

import { newEvent } from '../services/api.js'
import jwtDecode from 'jwt-decode'
import dataProveedor from '@/components/evento/dataProveedor'
import especies from '@/components/evento/especies'
import mapaEvento from '@/components/evento/mapaEvento'

export default {
  components: { dataProveedor, especies, mapaEvento },
  data () {
    return {
      dialog: false,
      e6: 3,
      tab: 0,
      mfnac: false,
      dataEventUpdate: [],
      selEvento: {},
      tiposEvento: [],
      tiposSubEvento: [],
      relEventoSubEvento: [],
      sisProds: [],
      dataProductor: [],
      dataMapa: [],
      dataEspecie: [],
      loading: false,
      dataEncabezado: {},
      // Rules
      rulesEventGeneral: [
        v => !!v || 'Campo obligatorio'
      ]
    }
  },
  created: function () {
    // let dataEvent = this.$route.params.dataEvent;
    // console.log(dataEvent);
    // this.dataEventUpdate = dataEvent;
  },
  async mounted () {
    // this.selEvento = this.$store.state.evento
    await this.getDataEvent()
  },
  methods: {
    onResultadosProductor (infoProductor) {
      this.dataProductor = infoProductor
    },
    onResultadosMapa (infoMapa) {
      this.dataMapa = infoMapa
      this.dataEncabezado.latitud = infoMapa.latitud
      this.dataEncabezado.longitud = infoMapa.longitud
      this.dataEncabezado.observacion = infoMapa.observacion
    },
    onResultadosEspecie (infoEspecie) {
      console.log(infoEspecie)
      this.dataEspecie = infoEspecie
    },
    addEspecie () {
      this.selEvento.especies.unshift({
        faseProd: '',
        espAfectada: '',
        espExtractiva: '',
        nombre: '',
        objetivo: '',
        fecha: '',
        densHectarea: '',
        areaSembrada: '',
        menuFecha: false
      })
    },
    delEspecie (esp) {
      const index = this.especies.findIndex(item => item === esp)
      this.selEvento.especies.splice(index, 1)
    },
    valEspecies (result) {
      if (result === false) {
        this.selEvento.especies = []
      }
    },
    async getDataEvent () {
      try {
        const res = await newEvent.getDataEvent()

        if (res.status === 200) {
          const encoded = res.data.token
          const decoded = jwtDecode(encoded)
          const data = decoded.dataEvent

          for (const documento in data.tipoEvento) {
            var addTipoEvento = {
              valor: data.tipoEvento[documento][0],
              text: data.tipoEvento[documento][1]
            }
            this.tiposEvento.push(addTipoEvento)
          }

          for (const documento in data.subEvento) {
            var addSubEvento = {
              valor: data.subEvento[documento][0],
              text: data.subEvento[documento][1],
              idEvento: data.subEvento[documento][2]
            }
            this.tiposSubEvento.push(addSubEvento)
          }

          for (const documento in data.sistemaProductivo) {
            var addSisProds = {
              valor: data.sistemaProductivo[documento][0],
              text: data.sistemaProductivo[documento][1]
            }
            this.sisProds.push(addSisProds)
          }
        }
      } catch (error) {
        console.log(error)
      }
    },
    cargarSubEventoSegunEvento () {
      this.relEventoSubEvento = this.tiposSubEvento.filter(subEvento => subEvento.idEvento === this.selEvento.tipoEv)
    },
    async guardarEvento () {
      this.loading = true
      this.validacionCamposObligatorios()
      if (!this.loading) { return }
      const dataEncabezadoEvento = this.dataEncabezadoEvento()
      const dataProductor = this.dataProductor
      const dataEspecie = this.dataEspecie

      console.log(dataEncabezadoEvento)

      try {
        const res = await newEvent.setDataEvento({ dataEncabezadoEvento, dataProductor, dataEspecie })

        if (res.status === 200) {
          const encoded = res.data.token
          const decoded = jwtDecode(encoded)
          const obj = decoded.userobj

          console.log(obj)

          this.$toasted.success('Se creo el evento correctamente.', { duration: 4000 })
        } else {
          console.log(res)
        }
      } catch (error) {
        this.$toasted.error('Ocurrio un error al guardar el evento.', { duration: 4000 })
        console.log(error)
      }

      this.loading = false
    },
    dataEncabezadoEvento () {
      // se captura el id del usuario del localStorage
      const dataUserLocal = JSON.parse(localStorage.getItem('dataUser'))
      this.dataEncabezado = this.selEvento
      this.dataEncabezado.latitud = this.dataMapa.latitud
      this.dataEncabezado.longitud = this.dataMapa.longitud
      this.dataEncabezado.departamento = this.dataMapa.departamento
      this.dataEncabezado.municipio = this.dataMapa.municipio
      this.dataEncabezado.precision = this.dataMapa.precision
      this.dataEncabezado.altitud = this.dataMapa.altitud
      this.dataEncabezado.enVereda = this.dataMapa.enVereda
      this.dataEncabezado.codVereda = this.dataMapa.codVereda
      this.dataEncabezado.nombrePuerto = this.dataMapa.nombrePuerto
      this.dataEncabezado.observacion = this.dataMapa.observacion
      this.dataEncabezado.idUsuario = dataUserLocal.id

      return this.dataEncabezado
    },
    validacionCamposObligatorios () {
      if (Object.keys(this.selEvento).length < 3) {
        this.$toasted.error('La información del detalle del evento esta incompleta.', { duration: 4000 })
        this.loading = false
      }
      if (Object.keys(this.dataMapa).length < 5) {
        this.$toasted.error('La información de la ubicación del evento esta incompleta.', { duration: 4000 })
        this.loading = false
      }
      if (Object.keys(this.selEvento.sisProds).length > 0 &&
          Object.keys(this.dataEspecie).length === 0) {
        this.$toasted.error('Hay sistemas productivos seleccionados pero no han sido registrados.', { duration: 4000 })
        this.loading = false
      }
    }
  },
  computed: {
    width () {
      let status

      switch (this.$vuetify.breakpoint.name) {
        case 'xs':
          status = true
          break
        case 'sm':
          status = true
          break
        case 'md':
          status = false
          break
        case 'lg':
          status = false
          break
        case 'xl':
          status = false
          break
      }

      return status
    },
    contForestal () {
      var valido = false
      for (const item in this.selEvento.sisProds) {
        if (valido) { break }
        valido = this.selEvento.sisProds[item] === 3
      }
      // this.valEspecies(result)
      return valido
    },
    esEmpresa () {
      var valido = false
      for (const item in this.selEvento.condJuridica) {
        if (valido) { break }
        valido = this.selEvento.condJuridica[item] === 2
      }
      return valido
    },
    compTipoDcto () {
      if (this.esEmpresa) return this.tiposDcto.filter(item => item.persona === 'juridica')
      return this.tiposDcto.filter(item => item.persona === 'natural')
    },
    especies () {
      return this.selEvento.especies
    }
  },
  watch: {
    /* contForestal (val) {
      console.log(val)
      if (!val) {
        this.selEvento.especies = []
      }
    } */
  }
}
</script>
<style scoped>
  .v-stepper--vertical .v-stepper__step {
    padding: 5px 5px 16px;
  }
  .v-application--is-ltr .v-stepper--vertical .v-stepper__content {
    margin: -8px -10px -16px 10px;
    padding: 16px 10px 10px 10px;
  }
</style>
