<template>
  <div>
    <v-card flat>
      <v-card-title style="display: inline; text-align: center">
        <v-banner>
          <v-icon
            size="80"
            color="blue darken-2"
            v-if="!updateUser">
            mdi-account-circle
          </v-icon>
          <v-icon
            size="80"
            color="blue darken-2"
            v-if="updateUser">
            mdi-account-edit
          </v-icon>
        </v-banner>
      </v-card-title>
      <v-card-text>
        <v-form
          lazy-validation
          ref="form"
          v-model="valid"
          >
          <v-container>
            <v-row :no-gutters="width">

              <v-col
                cols="12"
                :md="col"
                >
                <v-text-field
                  label="Nombres"
                  v-model="firstName"
                  outlined
                  dense
                  :rules="nameRules"
                  maxlength="100"
                  required
                  >
                </v-text-field>
              </v-col>

              <v-col
                cols="12"
                :md="col"
                >
                <v-text-field
                  label="Apellidos"
                  v-model="lastName"
                  outlined
                  dense
                  :rules="nameRules"
                  maxlength="100"
                  required
                  >
                </v-text-field>
              </v-col>

              <v-col
                cols="12"
                :md="col"
                >
                <v-text-field
                  label="No.Documento"
                  v-model="numDocument"
                  outlined
                  dense
                  :rules="documentRules"
                  maxlength="10"
                  required
                  onkeypress="return (event.charCode >= 48 && event.charCode <= 57)"
                  >
                </v-text-field>
              </v-col>

              <v-col
                cols="12"
                :md="col"
                >
                <v-select
                  label="Tipo Documento"
                  :items="typeDocuments"
                  item-value="valor"
                  item-text="text"
                  v-model="typeDocument"
                  :rules="generalRules"
                  outlined
                  dense
                  filled
                  >
                </v-select>
              </v-col>

              <v-col
                cols="12"
                :md="col"
                >
                <v-text-field
                 label="Institución"
                 v-model="institution"
                 outlined
                 dense
                 :rules="generalRules"
                 maxlength="100"
                 required
                 >
               </v-text-field>
              </v-col>

              <v-col
                cols="12"
                :md="col"
                >
                <v-text-field
                  label="Cargo"
                  v-model="workCenter"
                  outlined
                  dense
                  :rules="generalRules"
                  maxlength="100"
                  required
                  >
                </v-text-field>
              </v-col>

              <v-col
                cols="12"
                :md="col"
                >
                <v-text-field
                  label="Usuario"
                  v-model="usuario"
                  outlined
                  dense
                  :rules="usuarioRules"
                  required
                  >
                </v-text-field>
              </v-col>

              <v-col
                cols="12"
                :md="col"
                >
                <v-text-field
                  label="Email"
                  v-model="email"
                  outlined
                  dense
                  :rules="emailRules"
                  required
                  >
                </v-text-field>
              </v-col>

              <v-col
                cols="12"
                :md="col"
                >
              <v-text-field
                label="Contraseña"
                v-model="password"
                outlined
                type="password"
                dense
                :rules="passwordRules"
                required
                >
              </v-text-field>
              </v-col>

              <v-col
                cols="12"
                :md="col"
                >
                <v-text-field
                  label="Confirmar Contraseña"
                  v-model="checkPassword"
                  outlined
                  type="password"
                  dense
                  :rules="passwordRules"
                  required
                  >
                </v-text-field>
              </v-col>

             <!--<v-checkbox
              v-model="moderador"
              label="Moderator"
              style="margin: 0px !important"
              >
             </v-checkbox> -->
              <v-btn color="error" @click="update" style="width: 100%" v-if="updateUser">Actualizar</v-btn>
              <v-btn color="error" @click="submit" style="width: 100%" v-if="!updateUser">Registrar</v-btn>
              <v-row no-gutters align="center" class="my-3"  v-if="!updateUser">
                <v-divider></v-divider>
                <div class="mx-3">o</div>
                <v-divider></v-divider>
              <v-btn color="primary" @click="$emit('login')" style="width: 100%">volver</v-btn>
              <!-- <v-row justify="end">
                <v-btn text color="primary" small @click="$emit('login')">return</v-btn>
                <v-btn color="primary" small @click="submit">Send</v-btn>
              </v-row> -->
              </v-row>
            </v-row>
          </v-container>
        </v-form>
      </v-card-text>
    </v-card>
  </div>
</template>
<script>
// import
import { user } from '../../services/api'
import jwtDecode from 'jwt-decode'

export default {
  props: ['updateUser'],
  data () {
    return {
      // Data
      valid: true,
      firstName: '',
      lastName: '',
      numDocument: '',
      email: '',
      usuario: '',
      password: '',
      checkPassword: '',
      moderador: false,
      comentario: '',
      typeDocuments: [
        { text: 'Tarjeta de identidad', valor: '1' },
        { text: 'Cédula de Ciudadanía', valor: '2' },
        { text: 'Cédula de Extranjería', valor: '3' },
        { text: 'Pasaporte', valor: '4' },
        { text: 'Adulto sin identificación', valor: '5' },
        { text: 'Index', valor: '6' }
      ],
      typeDocument: '',
      institution: '',
      workCenter: '',

      // Rules
      nameRules: [
        v => !!v || 'Campo obligatorio',
        v => (v && v.length <= 100) || 'No más de 100 caracteres',
        v => (v && v.length >= 3) || 'No menos de 3 caracteres'
      ],
      documentRules: [
        v => !!v || 'El número de documento es requerido',
        v => (v && v.length <= 10) || 'No más de 10 caracteres',
        v => (v && v.length >= 10) || 'No menos de 10 caracteres'
      ],
      usuarioRules: [
        v => !!v || 'Campo obligatorio',
        v => (v && v.length <= 50) || 'No más de 50 caracteres'
      ],
      emailRules: [
        v => !!v || 'Campo obligatorio',
        v => /.+@.+\..+/.test(v) || 'E-mail no es valido'
      ],
      passwordRules: [
        v => !!v || 'Campo obligatorio',
        v => (v && v.length <= 15) || 'No más de 15 caracteres'
      ],
      generalRules: [
        v => !!v || 'Campo obligatorio',
        v => (v && v.length < 100) || 'No más de 100 caracteres'
      ]
    }
  },
  computed: {
    width () {
      if (this.updateUser === false) {
        return true
      }
      let status

      switch (this.$vuetify.breakpoint.name) {
        case 'xs':
          status = true
          break
        case 'sm':
          status = true
          break
        case 'md':
          status = false
          break
        case 'lg':
          status = false
          break
        case 'xl':
          status = false
          break
      }

      return status
    },
    col () {
      return (this.updateUser === true) ? 4 : 12
    }
  },
  async mounted () {
    /* try {
      const countries = await facebook.getCountries()
      console.log(countries)
      this.countries = countries.data.countries
    } catch (error) {
      console.log(error)
    } */
  },
  created () {
    if (this.updateUser === true) {
      const dataUserLocal = JSON.parse(localStorage.getItem('dataUser'))
      this.firstName = dataUserLocal.nombre
      this.lastName = dataUserLocal.apellido
      this.numDocument = dataUserLocal.nuDocumento
      this.typeDocument = { text: '' + dataUserLocal.nombreDocumento + '', valor: '' + dataUserLocal.idDocumento + '' }
      this.email = dataUserLocal.email
      this.usuario = dataUserLocal.usuario
      this.institution = dataUserLocal.institucion
      this.workCenter = dataUserLocal.cargo
    }
  },
  methods: {
    data () {
      const firstName = this.firstName
      const lastName = this.lastName
      const numDocument = this.numDocument
      const email = this.email
      const usuario = this.usuario
      const password = this.password
      const active = 'S'
      const pascheckPasswordsword = this.checkPassword
      const moderador = this.moderador
      const country = this.country
      const comentario = this.comentario
      const typeDocument = this.typeDocument
      const institution = this.institution
      const workCenter = this.workCenter
      const obj = {
        firstName,
        lastName,
        numDocument,
        email,
        usuario,
        password,
        pascheckPasswordsword,
        active,
        moderador,
        country,
        comentario,
        typeDocument,
        institution,
        workCenter
      }

      return obj
    },
    async submit () {
      if (this.$refs.form.validate()) {
        const obj = this.data()
        if (obj.password !== obj.pascheckPasswordsword) {
          this.$toasted.error('Verifique su contraseña.', { duration: 5000 })
          return
        }

        // this.$root.$emit('mainLoading', 'Loading...')
        try {
          const res = await user.createUser({ obj })

          if (res.status === 200) {
            const encoded = res.data.token
            const decoded = jwtDecode(encoded)
            const obj = decoded.userobj
            this.$session.start()
            this.$session.set('usuario', obj.usuario)
            this.$session.set('email', obj.email)
            this.$session.set('activo', obj.activo)
            this.$session.set('token', encoded)

            // almacenamiento en localStorage
            localStorage.setItem('dataUser', JSON.stringify(obj))

            this.$root.$emit('logged')
            this.$router.push('/home')
            this.$toasted.success('Solicitud recibida', { duration: 5000 })
          }
        } catch (error) {
          this.$toasted.error(error.response.data.message, { duration: 5000 })
        }
      } else {
        console.log('Errores en formulario')
      }
    },
    async update () {
      if (this.$refs.form.validate()) {
        const obj = this.data()
        if (obj.password !== obj.pascheckPasswordsword) {
          this.$toasted.error('Verifique su contraseña.', { duration: 5000 })
          return
        }
        // se captura el id del usuario del localStorage
        const dataUserLocal = JSON.parse(localStorage.getItem('dataUser'))
        obj.idUser = dataUserLocal.id

        // this.$root.$emit('mainLoading', 'Loading...')
        try {
          const res = await user.updateUser({ obj })
          console.log(res)
          if (res.status === 200) {
            const encoded = res.data.token
            const decoded = jwtDecode(encoded)
            const obj = decoded.userobj
            // this.$session.start()
            this.$session.set('usuario', obj.usuario)
            this.$session.set('email', obj.email)
            this.$session.set('activo', obj.activo)
            // this.$session.set('token', encoded)

            // almacenamiento en localStorage
            localStorage.setItem('dataUser', JSON.stringify(obj))

            this.$toasted.success('Solicitud recibida', { duration: 5000 })
          }
        } catch (error) {
          this.$toasted.error('No se logró enviar la solicitud', { duration: 5000 })
        }
      } else {
        console.log('Errores en formulario')
      }
    }
  }
}
</script>
<style scoped>
  .subtitulo {
    font-family: 'veneerregular';
    font-size: 2.4rem;
    line-height : 35px;
  }
</style>
