<template>
  <div>
    <v-row class="inicio" no-gutters justify="center" align="center" style="height: 100vh; background-color: white">
      <!-- <v-col lg="8" md="6" xs="0" align-self="center"
        v-if="height >= 500"
        style="background-color: #80DEEA; height: 100vh; display: grid;">
        <div style="align-self: center; margin-left: 100px">
          <v-img
            max-width="650"
            :src="require('../assets/Artboard.png')"
            >
          </v-img>
        </div>
        <div style="align-self: flex-end; text-align: -webkit-right;">
          <v-img
            max-width="680"
            :src="require('../assets/girlsWithMics.png')"
            >
          </v-img>
        </div>
      </v-col> -->
      <v-col lg="3" md="4" xs="12" align-self="center" class="px-5">
          <login @register="tipo='registro'" @recover="tipo='recover'" v-if="tipo === 'login'"></login>
          <registro @login="tipo='login'" v-if="tipo === 'registro'" :updateUser="false"></registro>
          <recPassword  @login="tipo='login'" v-if="tipo === 'recover'"></recPassword>
      </v-col>
    </v-row>
  </div>
</template>

<script>

import login from '@/components/inicio/login'
import registro from '@/components/inicio/registro'
import recPassword from '@/components/inicio/recPassword'

export default {
  components: { login, registro, recPassword },
  data () {
    return {
      tipo: 'login'
    }
  },
  computed: {
    medida () {
      if (this.tipo === 'registro') return { lg: 4, offsetlg: 4, md: 4, offsetmd: 4 }
      if (['login', 'recover'].includes(this.tipo)) return { lg: 6, md: 4, offsetmd: 4 }
      return {}
    },
    height () {
      switch (this.$vuetify.breakpoint.name) {
        case 'xs': return 220
        case 'sm': return 400
        case 'md': return 500
        case 'lg': return 600
        case 'xl': return 800
      }
      return 1200
    }
  }
}
</script>

<style scoped>
  .inicio {
    height: 100vh;
  }
</style>
