import Vue from 'vue'
import App from './App.vue'
import './registerServiceWorker'
import router from './router'
import store from './store'
import vuetify from './plugins/vuetify'
import '@babel/polyfill'
import VueSession from 'vue-session'
import axios from 'axios'
import Toasted from 'vue-toasted'
import VueApexCharts from 'vue-apexcharts'

Vue.use(VueApexCharts)
Vue.component('apexchart', VueApexCharts)
Vue.use(VueSession)
Vue.use(Toasted)
Vue.config.productionTip = false

axios.defaults.headers.Accept = 'application/json'
axios.defaults.baseURL = 'https://faobackend.southcentralus.azurecontainer.io:4000'// 'http://localhost:4000/'

var self = Vue.prototype

async function restoreSession () {
  const loggedIn = self.$session.exists()
  if (loggedIn) {
    // const usuario = self.$session.get('usuario')
    /* const userSexo = self.$session.get('user_sexo')
    const tokenExpira = self.$session.get('expires_at')
    const tokenType = self.$session.get('token_type') */
    const token = self.$session.get('token')
    // const fechaTokenExpira = self.moment(tokenExpira, 'YYYY-MM-DD HH:mm:ss')
    // eslint-disable-next-line no-undef
    // axios.defaults.headers.common['Authorization'] = `${tokenType} ${accessToken}`
    // axios.defaults.headers.Authorization = `${'Bearer'} ${token}`
    axios.defaults.params = { token: token }
  }
}

restoreSession()

new Vue({
  router,
  store,
  vuetify,
  render: h => h(App)
}).$mount('#app')
