<template>
  <div>

    <v-row justify="center">
      <v-img
      max-height="80"
      max-width="80"
      src="fao_logo_blue.png"
      ></v-img>
    </v-row>
    <br>

    <v-card
      flat
      elevation="4"
      >
      <v-card-title style="display: inline; text-align: center">
      </v-card-title>
      <v-card-text>
        <div class="font-weight-light"> LOGIN</div>
        <v-text-field
          placeholder="Email"
          v-model="email"
          outlined
          dense
          >
        </v-text-field>
        <v-text-field
          placeholder="Contraseña"
          v-model="password"
          outlined
          dense
          @click:append="show1 = !show1"
          :type="show1 ? 'text' : 'password'"
          :append-icon="show1 ? 'mdi-eye' : 'mdi-eye-off'"
          >
        </v-text-field>
        <p>Olvidates tu constraseña? click <v-btn color="primary" text style="padding: 2px" small @click="$emit('recover')">aquí</v-btn></p>
        <v-btn color="primary" @click="login" style="width: 100%">Iniciar</v-btn>
        <v-row no-gutters align="center" class="my-3">
          <v-divider></v-divider>
          <div class="mx-3">o</div>
          <v-divider></v-divider>
        </v-row>
        <v-btn color="secondary" @click="$emit('register')" style="width: 100%">Registrarse</v-btn>
      </v-card-text>
    </v-card>
  </div>
</template>
<script>
import { user } from '../../services/api'
import jwtDecode from 'jwt-decode'

export default {
  data () {
    return {
      email: '',
      password: '',
      show1: false
    }
  },
  methods: {
    async login () {
      const email = this.email
      const password = this.password
      this.$root.$emit('mainLoading', 'Loading...')
      try {
        const res = await user.loggin({ email, password })
        console.log(res)
        if (res.status === 200) {
          const encoded = res.data.token
          const decoded = jwtDecode(encoded)
          const obj = decoded.userobj
          console.log(obj)
          this.$session.start()
          this.$session.set('usuario', obj.usuario)
          this.$session.set('email', obj.email)
          this.$session.set('activo', obj.activo)
          this.$session.set('token', encoded)

          // almacenamiento en localStorage
          localStorage.setItem('dataUser', JSON.stringify(obj))

          this.$root.$emit('logged')
          this.$router.push('/home')
        }
      } catch (error) {
        console.log(error)
        this.$toasted.error('Credenciales incorrectas', { duration: 4000 })
      }
      this.$root.$emit('mainLoading', 'Loading...')
    }
  }
}
</script>
<style scoped>
  .subtitulo {
    font-family: 'veneerregular';
    font-size: 2.4rem;
    line-height : 35px;
  }
  .inputGOL {
    all: revert;
    border: solid 1px black;
    padding: 3px;
  }
</style>
