<template>
  <div>
    <v-container>

      <!-- Formulario para insertar nuevos productores -->
      <v-dialog
        v-model="dialog"
        fullscreen
        hide-overlay
        transition="dialog-bottom-transition"
      >

        <template v-slot:activator="{ on, attrs }">
          <v-btn
           color="primary"
           v-bind="attrs"
           v-on="on"
           title="Agregar un nuevo productor"
          >
          Agregar productor
          </v-btn>
        </template>

         <v-card>
          <v-toolbar
            dark
            color="primary"
          >
            <v-btn
              icon
              dark
              @click="dialog = false"
            >
              <v-icon>mdi-close</v-icon>
            </v-btn>
            <v-toolbar-title>Productor</v-toolbar-title>
            <v-spacer></v-spacer>
            <v-toolbar-items>
              <v-btn
                dark
                text
                @click="addProductor"
              >
                Agregar
              </v-btn>
            </v-toolbar-items>
          </v-toolbar>
      <br>
      <br>
      <br>
      <v-container>
      <v-row :no-gutters="width">

        <v-col
          cols="12"
          md="4"
          >
          <v-select
            label="Condición juridica del productor"
            :items="condJuridicas"
            v-model="infoProveedor.condJuridica"
            item-value="valor"
            dense
            outlined
            filled
            >
          </v-select>
        </v-col>

        <v-col
          cols="12"
          md="4"
          >
          <v-text-field
            :label="esEmpresa ? 'Nombre de la personería jurídica' : 'Nombres y apellidos'"
            v-model="infoProveedor.nombre"
            dense
            outlined
            filled
            >
          </v-text-field>
        </v-col>

        <v-col
          cols="12"
          md="4"
          >
          <v-select
            label="Tipo documento"
            :items="compTipoDcto"
            item-value="valor"
            v-model="infoProveedor.tipoDcto"
            dense
            outlined
            filled
            >
          </v-select>
        </v-col>

        <v-col
          cols="12"
          md="4"
          >
          <v-text-field
            label="Documento"
            v-model="infoProveedor.dcto"
            dense
            outlined
            filled
            >
          </v-text-field>
        </v-col>

        <v-row v-if="esEmpresa" :no-gutters="width" class="mt-0">
          <v-col
            cols="12"
            md="6"
            >
            <v-select
              label="Tipo de productor"
              :items="tiposProd"
              item-value="valor"
              v-model="infoProveedor.tipoProd"
              dense
              outlined
              filled
              >
            </v-select>
          </v-col>

          <v-col
            cols="12"
            md="6"
            >
            <v-select
              label="Tipo de relación con el predio"
              :items="tiposRelPre"
              item-value="valor"
              v-model="infoProveedor.relPre"
              dense
              filled
              outlined
              >
            </v-select>
          </v-col>
        </v-row>

        <v-col
          cols="12"
          md="4"
          >
          <v-text-field
            v-if="!esEmpresa"
            label="Direccion residencia"
            v-model="infoProveedor.dirRes"
            dense
            outlined
            filled
            >
          </v-text-field>
        </v-col>

        <v-col
          cols="12"
          md="4"
          >
          <v-text-field
            v-if="!esEmpresa"
            label="Numero telefonico"
            v-model="infoProveedor.tel"
            dense
            outlined
            filled
            >
          </v-text-field>
        </v-col>

        <v-col
          cols="12"
          md="4"
          >
          <v-select
            v-if="!esEmpresa"
            label="sexo"
            :items="listSexo"
            item-value="valor"
            v-model="infoProveedor.sexo"
            dense
            outlined
            filled
            >
          </v-select>
        </v-col>

        <v-menu
          v-if="!esEmpresa"
          v-model="mfnac"
          :close-on-content-click="false"
          :nudge-right="40"
          transition="scale-transition"
          offset-y
          min-width="auto"
          >
          <template v-slot:activator="{ on, attrs }">

            <v-col
              cols="12"
              md="4"
              >
              <v-text-field
                v-model="infoProveedor.fechaNac"
                label="Fecha nacimiento"
                readonly
                v-bind="attrs"
                v-on="on"
                outlined
                dense
                filled
                >
              </v-text-field>
            </v-col>
          </template>

          <v-col
            cols="12"
            md="4"
            >
            <v-date-picker
              v-model="infoProveedor.fechaNac"
              @input="mfnac = false"
              outlined
            ></v-date-picker>
          </v-col>

        </v-menu>

        <v-col
          cols="12"
          md="4"
          >
          <v-select
            v-if="!esEmpresa"
            label="Grupo étnico"
            :items="gruposEtnicos"
            item-value="valor"
            v-model="infoProveedor.gEtnico"
            dense
            filled
            outlined
            >
          </v-select>
        </v-col>

      </v-row>
      </v-container>
      </v-card>

      </v-dialog>

      <!-- Listado de productores agregados al evento -->
      <v-data-table
        :headers="productoresHeaders"
        :items="productores"
        :expanded.sync="expanded"
        single-expand
        item-key="nombre"
        show-expand
        class="elevation-1"
      >
        <template v-slot:top>
          <v-toolbar flat>
            <v-toolbar-title></v-toolbar-title>
          </v-toolbar>
        </template>
        <template v-slot:item.actions="{ item }">
          <!-- <v-icon
            small
            class="mr-2"
            @click="editProducto(item)"
          >
          mdi-pencil
          </v-icon> -->
          <v-icon
            small
            @click="deleteProductor(item)"
          >
          mdi-delete
          </v-icon>
        </template>
        <template v-slot:expanded-item="{ headers, item }">
          <td :colspan="headers.length">
            <v-row :no-gutters="width">
              <v-col
              cols="12"
              md="4"
              >
              <b>Tip.Document:</b> {{ item.tipoDcto }}
              </v-col>
              <v-col
              cols="12"
              md="4"
              >
              <b>Documento:</b> {{ item.dcto }}
              </v-col>
              <v-col
              cols="12"
              md="4"
              >
              <b>Tip.Productor:</b> {{ item.tipoProd }}
              </v-col>
              <v-col
              cols="12"
              md="4"
              >
              <b>Relacion Predio:</b> {{ item.relPre }}
              </v-col>
              <v-col
              cols="12"
              md="4"
              >
              <b>Dirección:</b> {{ item.dirRes }}
              </v-col>
              <v-col
              cols="12"
              md="4"
              >
              <b>Telefono:</b> {{ item.tel }}
              </v-col>
              <v-col
              cols="12"
              md="4"
              >
              <b>Sexo:</b> {{ item.sexo }}
              </v-col>
              <v-col
              cols="12"
              md="4"
              >
              <b>Nacimiento:</b> {{ item.fechaNac }}
              </v-col>
              <v-col
              cols="12"
              md="4"
              >
              <b>Grupo Etnico:</b> {{ item.gEtnico }}
              </v-col>
            </v-row>
          </td>
        </template>
      </v-data-table>

    </v-container>
  </div>
</template>
<script>

import { newEvent } from '../../services/api.js'
import jwtDecode from 'jwt-decode'

export default {
  name: 'dataProductor',
  props: ['obtenerData'],
  data () {
    return {

      // variables del sistema
      e6: 3,
      tab: 0,
      mfnac: false,
      infoProveedor: {},
      condJuridicas: [],
      tiposDcto: [],
      listSexo: [],
      gruposEtnicos: [],
      tiposProd: [],
      tiposRelPre: [],

      // variables de componentes
      // modal agregar nuevo productor
      dialog: false,
      notifications: false,
      sound: true,
      widgets: false,
      // tabla listado de productores agregados al evento
      expanded: [],
      productoresHeaders: [
        {
          text: 'Productor',
          align: 'start',
          sortable: false,
          value: 'nombre'
        },
        { text: 'Eliminar', value: 'actions', sortable: false, width: '2%' },
        { text: 'Ver más', value: 'data-table-expand', width: '2%' }
      ],
      productores: []
    }
  },
  async mounted () {
    await this.getDataProductor()
  },
  computed: {
    width () {
      let status

      switch (this.$vuetify.breakpoint.name) {
        case 'xs':
          status = true
          break
        case 'sm':
          status = true
          break
        case 'md':
          status = false
          break
        case 'lg':
          status = false
          break
        case 'xl':
          status = false
          break
      }

      return status
    },
    esEmpresa () {
      return (this.infoProveedor.condJuridica === 2)
    },
    compTipoDcto () {
      if (this.esEmpresa) {
        return this.tiposDcto.filter(item => item.persona === 'juridica')
      }
      return this.tiposDcto.filter(item => item.persona === 'natural')
    }
  },
  methods: {
    addProductor () {
      this.dialog = false
      if (Object.entries(this.infoProveedor).length === 0) {
        return
      }
      this.productores.push(this.infoProveedor)
      this.infoProveedor = {}
      this.obtenerData(this.productores)
    },
    deleteProductor (item) {
      console.log(item)
      const editedIndex = this.productores.indexOf(item)
      this.productores.splice(editedIndex, 1)
      this.obtenerData(this.productores)
    },
    // editProducto(item) {
    //   this.dialog = true
    //   this.editedIndex = this.desserts.indexOf(item)
    //   this.editedItem = Object.assign({}, item)
    //   this.dialog = true
    // },
    async getDataProductor () {
      try {
        const res = await newEvent.getDataProductor()

        if (res.status === 200) {
          const encoded = res.data.token
          const decoded = jwtDecode(encoded)
          const data = decoded.data

          for (const documento in data.tiposDocumento) {
            var addDocument = {
              valor: data.tiposDocumento[documento][0],
              text: data.tiposDocumento[documento][1],
              persona: data.tiposDocumento[documento][2]
            }
            this.tiposDcto.push(addDocument)
          }

          for (const documento in data.condicionJuridica) {
            var addConJuridica = {
              valor: data.condicionJuridica[documento][0],
              text: data.condicionJuridica[documento][1]
            }
            this.condJuridicas.push(addConJuridica)
          }

          for (const documento in data.sexo) {
            var addSexo = {
              valor: data.sexo[documento][0],
              text: data.sexo[documento][1]
            }
            this.listSexo.push(addSexo)
          }

          for (const documento in data.gruposEtnicos) {
            var addGrupoEtnico = {
              valor: data.gruposEtnicos[documento][0],
              text: data.gruposEtnicos[documento][1]
            }
            this.gruposEtnicos.push(addGrupoEtnico)
          }

          for (const documento in data.tipoProductor) {
            var addTipoProductor = {
              valor: data.tipoProductor[documento][0],
              text: data.tipoProductor[documento][1]
            }
            this.tiposProd.push(addTipoProductor)
          }

          for (const documento in data.tipoRelacionPredio) {
            var addTipoRelacion = {
              valor: data.tipoRelacionPredio[documento][0],
              text: data.tipoRelacionPredio[documento][1]
            }
            this.tiposRelPre.push(addTipoRelacion)
          }
        }
      } catch (error) {
        console.log(error)
      }
    }
  }
}
</script>
